import BuildPreFabe from "../Crl/BuildPreFabe"
import GameLogic from "../Crl/GameLogic"
import WxApi from "../Libs/WxApi"
import SoundMgr from "../Mod/SoundMgr"
import SGConfig from "../SGSDK/SGConfig"
import SGMgr from "../SGSDK/SGMgr"

export default class LoadingUI extends Laya.Scene {
    constructor() {
        super()
    }

    bar: Laya.ProgressBar
    logo: Laya.Image;


    resLoaded: boolean = false
    sdkInited: boolean = false

    //perNum: Laya.Label
    _platform: any = Laya.Browser.window.wx;
    packageNameArr: string[] = ['LayaScene_SampleScene', 'Sounds', 'assets'];

    onOpened() {
        console.log("进入加载页");
        this.initPlatform();
        WxApi.initPlatform();

        if (SGConfig.isWechat && this.logo.visible) {
            let path = "loadingUI/" + Laya.Browser.window['wx'].getAccountInfoSync().miniProgram.appId + ".png";
            this.logo.skin = path;
        }
        this.size(Laya.stage.displayWidth, Laya.stage.displayHeight)
        if (!Laya.Browser.onWeiXin && !Laya.Browser.onQGMiniGame && !Laya.Browser.onVVMiniGame) {
            this.loadRes();
        }
        else {
            console.log("进入分包")
            this.loadSubpackage();
        }

        Laya.timer.frameLoop(1, this, () => {
            this.bar.value += 0.002
            if (this.resLoaded && this.sdkInited) {
                GameLogic.Share.initScene()
                Laya.timer.clearAll(this)
            }
        })

        SGMgr.init(() => {
            this.sdkInited = true
        })
    }

    onClosed() {

    }
    initPlatform() {
        if (Laya.Browser.onWeiXin) {
            this._platform = Laya.Browser.window.wx;
        }
        else if (Laya.Browser.onQGMiniGame) {
            this._platform = Laya.Browser.window.qg;
        }
        else if (Laya.Browser.onVVMiniGame) {
            this._platform = Laya.Browser.window.qg;
        }
        else if (Laya.Browser.onTTMiniGame) {
            this._platform = Laya.Browser.window.tt;
        }
    }

    //#region 分包
    subpackageLoadNum: number = 0;


    loadSubpackage() {
        console.log(this._platform);
        this._platform.loadSubpackage({
            name: this.packageNameArr[this.subpackageLoadNum], // name 可以填 name 或者 root
            success: (res) => {
                // 分包加载成功后通过 success 回调
                this.subpackageLoadNum++;
                if (this.subpackageLoadNum < this.packageNameArr.length) {
                    this.loadSubpackage();
                } else {
                    this.loadRes()
                }
            }
        })
    }

    // loadSubpackage() {
    //     Laya.Browser.window.wx.loadSubpackage({
    //         name: 'unity', // name 可以填 name 或者 root
    //         success: (res) => {
    //             // 分包加载成功后通过 success 回调
    //             this.loadSubpackage_1();
    //         }
    //     })
    // }

    // loadSubpackage_1() {
    //     Laya.Browser.window.wx.loadSubpackage({
    //         name: 'unity_round', // name 可以填 name 或者 root
    //         success: (res) => {
    //             // 分包加载成功后通过 success 回调
    //             this.loadSound();
    //         }
    //     })
    // }

    // loadSound() {
    //     Laya.Browser.window.wx.loadSubpackage({
    //         name: 'sound', // name 可以填 name 或者 root
    //         success: (res) => {
    //             // 分包加载成功后通过 success 回调
    //             this.loadRes()
    //         }
    //     })
    // }

    loadRes() {
        //预加载3d资源
        console.log("非小游戏加载资源");
        var resUrl = [
            WxApi.UnityPath + 'SampleScene.lh',
            // WxApi.UnityPath_round + 'LevelRound.lh',
        ];
        Laya.loader.create(resUrl, Laya.Handler.create(this, () => {
            this.resLoaded = true;
        }), Laya.Handler.create(this, this.onProgress));
    }


    onComplete() {
        this.resLoaded = true
    }

    onProgress(value) {
        //this.bar.value = value
        //this.perNum.text = (50 + Math.floor(value * 50)).toString() + '%'
    }
}