    /**转盘转动 */
    RotationBoxAnim() {
        this.RotationBox.rotation += 10;
    }

    /**灯光急闪 */
    DrawLightLoopAnim() {
        this.DrawLight.visible = !this.DrawLight.visible;
    }

    /**指针抖动 */
    Shake(isClean: boolean = false) {
        return;
        if (isClean) {
            Laya.timer.clearAll(this.Pointer);
            Laya.Tween.clearAll(this.Pointer);
            Laya.Tween.to(this.Pointer, { rotation: 0 }, 100, Laya.Ease.linearIn);
            return;
        }
        Laya.Tween.to(this.Pointer, { rotation: -10 }, 100, Laya.Ease.linearIn, Laya.Handler.create(this, () => {
            Laya.Tween.to(this.Pointer, { rotation: 10 }, 100, Laya.Ease.linearIn, Laya.Handler.create(this, () => {
                Laya.Tween.to(this.Pointer, { rotation: 0 }, 100, Laya.Ease.linearIn, Laya.Handler.create(this, () => {
                    let func = () => {
                        this.Shake(isClean);
                    }
                    this.Pointer.timerOnce(1000, this, func);
                }));
            }));
        }));
    }

