    /**今天是否可以抽奖 */
    TodayCanDraw(): boolean {
        let today = DateManager.instance.formatDate();
        let canSign = DateManager.instance.formatDateAdd(this.LastSignStamp, 1);
        let todaytime = today.split("-");
        let canSigntime = canSign.split("-");
        let year: boolean = false;
        let month: boolean = false;
        let day: boolean = false;
        //判断年份是否大于
        year = parseInt(todaytime[0]) > parseInt(canSigntime[0]) ? true : false;
        if (year) {
            return true;
        }
        //判断同年不同月是否大于
        year = parseInt(todaytime[0]) == parseInt(canSigntime[0]) ? true : false;
        month = parseInt(todaytime[1]) > parseInt(canSigntime[1]) ? true : false;
        if (year && month) {
            return true;
        }
        //判断同年同月和同天数或者天数大于
        year = parseInt(todaytime[0]) == parseInt(canSigntime[0]) ? true : false;
        month = parseInt(todaytime[1]) == parseInt(canSigntime[1]) ? true : false;
        day = parseInt(todaytime[2]) >= parseInt(canSigntime[2]) ? true : false;
        if (year && month && day) {
            return true
        }
        return false;
    }
