    onOpenBtnCB() {
        this.OpenBtn.offAll();
        this.CloseBtn.offAll();
        this.Shake();
        Laya.timer.loop(1, this, this.RotationBoxAnim);
        Laya.timer.loop(100, this, this.DrawLightLoopAnim);
        SoundMgr.instance.playSoundEffect("WheelTickVar01");
        Laya.timer.once(3000, this, () => {
            // GameLogic.Share.LuckDrawNeedNum--;
            Laya.timer.clear(this, this.RotationBoxAnim);
            let random = Utility.GetRandom(0, 7);
            let addrot = this.RotationArr[random];
            let rot = Math.floor(this.RotationBox.rotation / 360);
            Laya.Tween.to(this.RotationBox, { rotation: rot * 360 + addrot }, 1000, Laya.Ease.backOut, Laya.Handler.create(this, () => {
                SoundMgr.instance.playSoundEffect("WheelStop");
                GameLogic.Share.SpawnUIAddCoin(this.OpenBtn, this.CoinIcon, this, this, false);
                Laya.timer.once(1000, this, () => {
                    PlayerDataMgr.getPlayerData().coin += this.CoinArr[random];
                    PlayerDataMgr.setPlayerData();
                    this.closeBtnCB();
                })
            }));
        })
    }

