import PlayerDataMgr from "../../Libs/PlayerDataMgr";
import EventMgr, { EventType } from "../../Mod/EventManager";
import Utility from "../../Mod/Utility";
import GameLogic from "../GameLogic";
import PlayerController from "../role/PlayerController";

export default class DiamondController extends Laya.Script3D {

    Display: Laya.Sprite3D;
    Gem: Laya.Sprite3D

    onAwake() {
        this.Display = this.owner as Laya.Sprite3D;
        this.Gem = Utility.FindChildByName("Gem", this.Display) as Laya.Sprite3D;
    }


    GetDiamond() {
        this.Display.getComponent(Laya.PhysicsCollider).enable = false;
        this.Gem.active = false;
        PlayerDataMgr.getPlayerData().diamond += 1;
        PlayerDataMgr.setPlayerData();
        EventMgr.SendEvent(EventType.onUpdateDiamondBox);
    }

    isDestroy: boolean = false;
    onDestroy(): void {
        this.isDestroy = true;
    }

    onUpdate(): void {
        if (this.isDestroy) return;
        this.Gem.transform.localRotationEulerY -= 1;
        // this.isGetMoney();
    }
}