    PushMovePath(movePath: Laya.Sprite3D, ctr: TraceProgressTracker, player: Laya.Sprite3D) {
        // console.log(this.isInLeftOrRight, "<<<<<<<<<<<<<<<<<<<");
        // if (this.isInLeftOrRight) return;
        let circuit = movePath.addComponent(TraceCircuit) as TraceCircuit;
        //取点
        // console.log(this.isLeftOrRight);
        let path: any = movePath;
        let _ctr: TraceProgressTracker;
        switch (player.name) {
            case "Player":
                _ctr = this._ctr;
                break;
            case "AI_1":
                _ctr = this._ctr_AI_1;
                break;
            case "AI_2":
                _ctr = this._ctr_AI_2;
                break;
            case "AI_3":
                _ctr = this._ctr_AI_3;
                break;
        }
        path._children.forEach(element => {
            let subName = element.name.split("_");
            // console.log(element, subName.length >= 3);
            if (subName.length >= 3) {
                console.log("抛飞路线", element._children, _ctr.speedFactor);
                let num = 0;
                if (_ctr.speedFactor >= 0.1 && _ctr.speedFactor < 0.6) {
                    num = 0;
                }
                else if (_ctr.speedFactor >= 0.6 && _ctr.speedFactor < 1) {
                    num = 1;
                }
                else if (_ctr.speedFactor >= 1) {
                    num = 2;
                }
                element._children[num]._children.forEach(element3 => {
                    circuit.allPointVec3.push(element3.transform.position.clone());
                    circuit.allRotVec3.push(element3.transform.rotationEuler.clone())
                });
            } else {
                element._children.forEach(element2 => {
                    circuit.allPointVec3.push(element2.transform.position.clone());
                    circuit.allRotVec3.push(element2.transform.rotationEuler.clone())
                });
            }
        });
        circuit.init();
        _ctr.circuit = circuit;
        _ctr.target = player.transform;
    }

    createLevel() {
        this.isPlayerTurn = true;
        this.hadWalker = false;
        this.canDestroyTower = true;
        this.playerNum = 0;

        GameManager.Instance.AllPlayerRank = [];


        let curGrade: number = Math.floor((PlayerDataMgr.getPlayerData().grade - 1) % 5);

        let url = "assets/Level_" + (curGrade + 1) + ".json";
        Laya.loader.create(url, new Laya.Handler(this, this.JsonBack, [url]));
        // this.onInitLevelRound(1);
        this.onInitPlayer(curGrade + 1);
        // let levelRes: Laya.Sprite3D = res.getChildByName('Level' + (curGrade + 1)) as Laya.Sprite3D;

        // Laya.timer.loop(100, this, this.checkIsGameOver)
    }

