    WagonisLeftOrRight: number = 0;
    speedFactor: number = 0.1;
    onInitPlayer(level: number) {
        this._player = null;
        this._movePath = null;
        this._ctr = null;
        this._player = (BuildPreFabe.GameScene.getChildByName("Player") as Laya.Sprite3D).clone() as Laya.Sprite3D;
        this._movePath = (BuildPreFabe.GameScene.getChildByName("Path_Level_" + level) as Laya.Sprite3D).clone() as Laya.Sprite3D;
        this._player.addComponent(PlayerController);
        // let playerctr = this._player.getComponent(PlayerController) as PlayerController;
        // playerctr.init();
        this._ctr = this._player.addComponent(TraceProgressTracker) as TraceProgressTracker;
        // ctr.PathSprite = movePath;
        this._levelNode.addChild(this._player);
        // this._player.transform.position = new Laya.Vector3(0, 0, 0);
        this._levelNode.addChild(this._movePath);
        this.PushMovePath(this._movePath, this._ctr, this._player);
        this._ctr.speedFactor = this.speedFactor = 0;
        this._ctr.init(2);
        this._ctr.initCtrTarget(this._player);

        CameraCrl.Instance.CameraTarget = PlayerController.Instance.Display;

        this._player.addChild(this._camera);
        this._camera.transform.localPosition = new Laya.Vector3(0, 0, 0);
        this._camera.transform.localRotationEuler = new Laya.Vector3(0, 0, 0);

        CameraCrl.Instance.CameraState = CameraState.GameStart;

        this.AllAI = [];
        // return;
        for (let i = 1; i < 3; i++) {
            this.onInitAI(level, i);
        }
    }

    _ctr_AI_1: TraceProgressTracker;
    _ctr_AI_2: TraceProgressTracker;
    _ctr_AI_3: TraceProgressTracker;
    AllAI: Laya.Sprite3D[] = [];
    onInitAI(level: number, index: number) {
        let _player = null;
        this['_movePath_AI_' + index] = null;
        this['_movePath_AI_' + index] = (BuildPreFabe.GameScene.getChildByName("Path_Level_" + level) as Laya.Sprite3D).clone() as Laya.Sprite3D;
        this['_ctr_AI_' + index] = null;
        _player = (BuildPreFabe.GameScene.getChildByName("AI") as Laya.Sprite3D).clone() as Laya.Sprite3D;
        _player.name = "AI_" + index;
        let ctr = _player.addComponent(AIController) as AIController;
        ctr._index = index;
        switch (index) {
            case 1:
                ctr._roateNum = -0.8;
                break;
            case 2:
                ctr._roateNum = 0.8;
                break;
        }
        ctr._roateNum
        // let playerctr = this._player.getComponent(PlayerController) as PlayerController;
        // playerctr.init();
        this['_ctr_AI_' + index] = _player.addComponent(TraceProgressTracker) as TraceProgressTracker;
        // ctr.PathSprite = movePath;
        this._levelNode.addChild(_player);
        this._levelNode.addChild(this['_movePath_AI_' + index]);
        this.PushMovePath(this['_movePath_AI_' + index], this['_ctr_AI_' + index], _player);
        this['_ctr_AI_' + index].speedFactor = 0;
        this['_ctr_AI_' + index].init(2);
        this['_ctr_AI_' + index].initCtrTarget(_player);


        this.AllAI.push(_player);
        ctr.init();
        if (!this.isRest)
            _player.active = false;
    }


    private jb_pos: Laya.Vector3 = new Laya.Vector3();
    private jb_eluer: Laya.Vector3 = new Laya.Vector3();
    private jb_scale: Laya.Vector3 = new Laya.Vector3();
    public _map: Laya.Sprite3D = null;
    JsonBack(url) {
        let Info = Laya.loader.getRes(url);
        let clone: Laya.Sprite3D;
        let ctr;

        let coinArr: CoinController[] = [];

        for (var item of Info.data) {
            // console.log("clone  ", item.N);
            clone = PreFabCtr.instance.getPreFabClone(item.N) as Laya.Sprite3D;
            this.jb_pos.x = -item.PX;
            this.jb_pos.y = item.PY;
            this.jb_pos.z = item.PZ;
            this.jb_eluer.x = item.RX;
            this.jb_eluer.y = -item.RY;
            this.jb_eluer.z = -item.RZ;
            this.jb_scale.x = item.SX;
            this.jb_scale.y = item.SY;
            this.jb_scale.z = item.SZ;
            clone.transform.rotationEuler = this.jb_eluer;
            clone.transform.setWorldLossyScale(this.jb_scale);
            clone.transform.position = this.jb_pos;
            ctr = null;
            switch (item.N) {
                case Obstacle.Gold_1:
                    let coinCtr = clone.addComponent(CoinController) as CoinController;
                    coinArr.push(coinCtr);
                    break;
                case Obstacle.Gem_1:
                    clone.addComponent(DiamondController) as DiamondController;
                    break;
                case Obstacle.Ball_1:
                    clone.addComponent(BallController) as BallController;
                    break;
                case Obstacle.SwimmingPool_1:
                    clone.addComponent(SwimmingPoolController_1) as SwimmingPoolController_1;
                    break;
                case Obstacle.BNowlingBottle_All:
                    clone.addComponent(BNowlingBottleController) as BNowlingBottleController;
                    break;
                case Obstacle.Tower_1:
                    clone.addComponent(TowerController) as TowerController;
                    break;
                case Obstacle.Box_1:
                    clone.addComponent(BoxController) as BoxController;
                    break;
                case Obstacle.MES_RailStop:
                    clone.addComponent(MESRailStopController) as MESRailStopController;
                    break;
                case Obstacle.MES_DontMove:
                    clone.addComponent(MESDontMoveController) as MESDontMoveController;
                    break;
                case Obstacle.MES_LongObstacle_Horizontale:
                    clone.addComponent(MESLongObstacleHorizontaleController) as MESLongObstacleHorizontaleController;
                    break;
                case Obstacle.MES_Bombe:
                    clone.addComponent(MESBombeController) as MESBombeController;
                    break;
            }
            clone.active = true;
            this._map.addChild(clone);
            if (item.N == Obstacle.Box_1) {
                clone.getComponent(BoxController).onInit();
            }
        }
        let random = 0;
        let num = 5;
        for (let i = 0; i < coinArr.length; i++) {
            let _coin = coinArr[i];
            if (random == 0) {
                _coin.onChangeCoinPos(0);
                num--;
            } else if (random == 1) {
                _coin.onChangeCoinPos(5);
                num--;
            } else if (random == 2) {
                _coin.onChangeCoinPos(-5);
                num--;
            }
            if (num <= 0) {
                num = 5;
                random = Utility.GetRandom(0, 2);
            }
        }
        console.log();
    }

