    /**
     * 生成金币UI
     * @param pos 起点UI-Image/Label 之类
     * @param taget 终点UI-Image/Label 之类
     * @param par 父物体-选界面
     * @param viewSrc 界面脚本
     * @param isNeedParent 是否需要转化父节点
     */
    SpawnUIAddCoin(pos, taget, par, viewSrc, isNeedParent) {
        let effect: Laya.Box = Laya.Pool.getItemByCreateFun("glodpre", this.addGold_Pre.create, this.addGold_Pre);
        par.addChild(effect);
        effect.x = effect.y = 0;
        effect.visible = true;
        var localPos = new Laya.Point();
        var SterPos = new Laya.Point();
        if (isNeedParent) {
            taget.parent.localToGlobal(localPos);
            pos.parent.localToGlobal(SterPos);
        } else {
            taget.localToGlobal(localPos);
            pos.localToGlobal(SterPos);
        }
        SoundMgr.instance.playSoundEffect("CoinPick");
        for (let i = 0; i < effect.numChildren; i++) {
            let childItem = effect.getChildAt(i) as Laya.Image;
            // childItem.pos(this.GetPos(pos, par, "x"), this.GetPos(pos, par, "y"));
            childItem.pos(SterPos.x, SterPos.y);
            Laya.Tween.to(childItem, { x: childItem.x + Math.random() * 200 - 100, y: childItem.y + Math.random() * 200 - 100, }, 300, null,
                Laya.Handler.create(viewSrc, () => {
                    Laya.Tween.to(childItem, { x: localPos.x, y: localPos.y }, 500, null, Laya.Handler.create(viewSrc, () => {
                        childItem.visible = false;
                    }));
                }))
        }
    }

