    bIndex_1: number = 0;
    bemonve_1(ow: Laya.Image, pointArr: Laya.Vector3[], index: number, viewSrc) {
        let endpos = pointArr[index];
        // console.log(endpos);
        Utility.UI2DPositionTween(ow, new Laya.Point(endpos.x, endpos.y), 10, Laya.Ease.linearIn, viewSrc, () => {
            index++;
            if (index >= 50) {
                ow.visible = false;
                // Laya.timer.clearAll(viewSrc);
                return;
            }
            this.bemonve_1(ow, pointArr, index, viewSrc);
        });
    }

    GetPos(pos, par, re) {
        let posPar = pos.parent;
        let x = pos[re];
        while (posPar != par) {
            x += posPar[re];
            posPar = posPar.parent;
        }
        return x;
    }
