    gameOver(isWin: boolean) {
        if (this.isGameOver) return;
        Laya.timer.clearAll(this)
        WxApi.DoVibrate(false)
        if (isWin) {
            SoundMgr.instance.playSoundEffect('LevelCompleted')
        } else {
            SoundMgr.instance.playSoundEffect('LevelFailed')
        }
        this.isWin = isWin
        this.isGameOver = true
        this.isStartGame = false
        // Laya.Scene.close('MyScenes/GameUI.scene')
        Laya.timer.once(2000, this, () => {
            // for (let i = 0; i < this.AllAI.length; i++) {
            //     let ctr = this.AllAI[i].getComponent(AIController) as AIController;
            //     if (ctr.isStop) continue;
            //     ctr.isStop = true;
            //     GameManager.Instance.AllPlayerRank.push(this.AllAI[i]);
            // }
            SGMgr.gameOver(() => {
                Laya.Scene.open('MyScenes/FinishUI.scene', false)
            })
        })
    }

    ReviveGame() {
        this.isStartGame = true;
        this.isGameOver = false;
        this.isWin = false;
        this.isFinish = false;
        this.isPause = false;
        GameManager.Instance.PlayerHpNum = 3;
        EventMgr.SendEvent(EventType.onChagePlayerHpBox);
        CameraCrl.Instance.CameraState = CameraState.Game;
        PlayerController.Instance.isDie = false;
        PlayerController.Instance.isInSprint = false;
        PlayerController.Instance.isInWater = false;
        PlayerController.Instance.onAddSpeedCB();
        EventMgr.SendEvent(EventType.AIStartGame);
        Laya.Scene.close('MyScenes/FinishUI.scene');
    }

    isRest: boolean = false;
    restartGame(isRest: boolean = false) {
        GameLogic.Share._cameraCrl.FX_Fire1.active = false;
        this.isStartGame = false;
        this.isGameOver = false;
        this.isWin = false;
        this.isPause = false;
        this._camera.fieldOfView = this.startCamField;
        this.isFinish = false;

        this._scene.addChild(this._camera);
        this._camera.transform.position = this.camStartPos;
        this._camera.transform.rotation = this.camStartRotation;

        this.isRest = isRest;

        this._map.destroyChildren();
        this._levelNode.destroyChildren();
        this.createLevel();
    }
