import PlayerDataMgr from "../../Libs/PlayerDataMgr";
import PhysicsCrushAnim from "../../Mod/3D/PhysicsCrushAnim";
import Utility from "../../Mod/Utility";
import GameLogic from "../GameLogic";
import PlayerController from "../role/PlayerController";

export default class BallController extends Laya.Script3D {

    Display: Laya.Sprite3D;
    Ball: Laya.Sprite3D

    onAwake() {
        this.Display = this.owner as Laya.Sprite3D;
        this.Ball = Utility.FindChildByName("Ball", this.Display) as Laya.Sprite3D;
    }

    // onInitAnim() {
    //     Ball
    // }

    onPushBall() {
        let randomX = Utility.GetRandom(0, 0, false);
        let randomY = Utility.GetRandom(0, 5, false);
        let randomZ = Utility.GetRandom(3, 5, false);
        let block = this.Ball;
        this.Display.getComponent(Laya.PhysicsCollider).enabled = false;
        let ctr = block.addComponent(PhysicsCrushAnim) as PhysicsCrushAnim;
        if (Utility.GetRandom(1, 2) == 1) {
            randomX = -randomX;
            randomZ = -randomZ;
        }
        let BezierPoints = Utility.BezierPoints(
            block.transform.localPosition,
            new Laya.Vector3(block.transform.localPosition.x, block.transform.localPosition.y + randomY, block.transform.localPosition.z + (randomZ / 2)),
            new Laya.Vector3(block.transform.localPosition.x, block.transform.localPosition.y, block.transform.localPosition.z + randomZ),
            50
        );
        ctr.bemonve_1(block, BezierPoints, this, () => {
            block.active = false;
        });
    }

    onUpdate(): void {
    }
}