import SGAD from "./SGAD"
import SGConfig from "./SGConfig"

export default class SGMgr {
    private static gameCount: number = 1

    static isVibrate: boolean = true
    static firstStart: boolean = true

    private static adLoaded: boolean = false
    private static sdkLoaded: boolean = false

    static init(cb: Function) {
        if (!Laya.Browser.onWeiXin && !Laya.Browser.onQGMiniGame && !Laya.Browser.onVVMiniGame) {
            cb && cb()
            return
        }
        SGConfig.initPlatform();
        if (Laya.Browser.onWeiXin) {
            SGConfig.initConfigData(() => {
                SGAD.initAd(() => {
                    this.adLoaded = true;
                })
                this.sdkLoaded = true;
                if (SGConfig.data.front_interstitial_onHide_switch) {
                    Laya.Browser.window.wx.onShow(() => { SGAD.showInterstitialAd() })
                }
            })
        } else {
            SGAD.initAd(() => {
                this.adLoaded = true;
            })
            this.sdkLoaded = true;
        }

        let fun = () => {
            if (this.adLoaded && this.sdkLoaded) {
                Laya.timer.clear(this, fun)
                this.showLoading(cb)
            }
        }
        Laya.timer.loop(100, this, fun)
    }

    static showLoading(cb?: Function) {
        if (!Laya.Browser.onWeiXin && !Laya.Browser.onQGMiniGame && !Laya.Browser.onVVMiniGame) {
            cb && cb();
            return
        }
        if (Laya.Browser.onWeiXin) {
            this.showRemen(RemenIndex.Remen_Loading, cb);
        }
        else if (Laya.Browser.onQGMiniGame || Laya.Browser.onVVMiniGame) {
            cb && cb();
            this.visibleLoadingUI(true, "_OPPO")
        }
    }

    static inHome() {
        if (!Laya.Browser.onWeiXin && !Laya.Browser.onQGMiniGame && !Laya.Browser.onVVMiniGame) { return }
        // let path = "";
        if (Laya.Browser.onWeiXin) {
            if (SGConfig.isShowHomeBanner) SGAD.showBannerAd();
            if (SGConfig.isShowHomeSideGrid) SGAD.visibleSideGridAd(true);
            this.visibleHomeUI(true);
        }
        else if (Laya.Browser.onQGMiniGame) {
            this.visibleLoadingUI(false, "_OPPO");
            this.visibleHomeUI(true, "_OPPO");
            SGAD.showBannerAd();
            if (this.firstStart) {
                this.firstStart = false;
                Laya.timer.once(100, this, () => {
                    this.visiblePrivacyUI(true, "_OPPO");
                })
            }
        }
        else if (Laya.Browser.onVVMiniGame) {
            this.visibleLoadingUI(false, "_VIVO");
            this.visibleHomeUI(true, "_VIVO");
            SGAD.showBannerAd();
            if (this.firstStart) {
                this.firstStart = false;
                Laya.timer.once(100, this, () => {
                    this.visiblePrivacyUI(true, "_VIVO");
                })
            }
        }
    }
    static inShop() {
        if (!Laya.Browser.onWeiXin && !Laya.Browser.onQGMiniGame && !Laya.Browser.onVVMiniGame) { return }
        let path = "";
        if (Laya.Browser.onWeiXin) {
            if (SGConfig.isShowShopBanner) SGAD.showBannerAd();
            SGAD.visibleSideGridAd(false);
        }
        else if (Laya.Browser.onQGMiniGame) {
            path = "_OPPO";
        }
        else if (Laya.Browser.onVVMiniGame) {
            path = "_VIVO";
        }
        SGAD.hideBannerAd();
        this.visibleHomeUI(false, path);
    }
    static moreGame() {
        SGAD.hideBannerAd()
        SGAD.visibleSideGridAd(false)
        this.showRemen(RemenIndex.Remen_Start, () => {
            if (SGConfig.isShowHomeBanner) SGAD.showBannerAd()
            if (SGConfig.isShowHomeSideGrid) SGAD.visibleSideGridAd(true)
        }, true)
    }
    static startGame(cb?: Function) {
        if (!Laya.Browser.onWeiXin && !Laya.Browser.onQGMiniGame && !Laya.Browser.onVVMiniGame) { cb && cb(); return; }
        let path = "";
        if (Laya.Browser.onWeiXin) {
            SGAD.hideBannerAd();
            SGAD.visibleSideGridAd(false);
            this.showRemen(RemenIndex.Remen_Start, cb);
        }
        else if (Laya.Browser.onQGMiniGame) {
            path = "_OPPO";
            SGAD.hideBannerAd();
            cb && cb();
        }
        else if (Laya.Browser.onVVMiniGame) {
            path = "_VIVO";
            SGAD.hideBannerAd();
            cb && cb();
        }
        this.visibleHomeUI(false, path);
    }
    static inGame() {
        if (!Laya.Browser.onWeiXin && !Laya.Browser.onQGMiniGame && !Laya.Browser.onVVMiniGame) { return; }
        if (Laya.Browser.onWeiXin) {
            SGAD.hideBannerAd();
            if (SGConfig.isShowGameBanner) SGAD.showBannerAd();
            if (SGConfig.isShowGameSingleGrid) SGAD.visibleGameGridAd(true);
        } else {
            SGAD.showBannerAd();
        }
    }
    static gameOver(cb?: Function) {
        if (!Laya.Browser.onWeiXin && !Laya.Browser.onQGMiniGame && !Laya.Browser.onVVMiniGame) { cb && cb(); return; }
        if (Laya.Browser.onWeiXin) {
            SGAD.hideBannerAd();
            SGAD.visibleGameGridAd(false);;
            this.showRemen(RemenIndex.Remen_Over, cb);
        }
        else if (Laya.Browser.onQGMiniGame) {
            SGAD.hideBannerAd();
            this.showNativeUI(true, "_OPPO", cb);
        }
        else if (Laya.Browser.onVVMiniGame) {
            SGAD.hideBannerAd();
            cb && cb();
        }
    }
    static inFinish() {
        if (!Laya.Browser.onWeiXin && !Laya.Browser.onQGMiniGame && !Laya.Browser.onVVMiniGame) { return; }
        SGAD.hideBannerAd();
        if (Laya.Browser.onWeiXin) {
            if (SGConfig.isShowFinishBanner) SGAD.showBannerAd();
            if (SGConfig.isShowFinishSideGrid) SGAD.visibleSideGridAd(true);
        }
        else if (Laya.Browser.onQGMiniGame) {
            SGAD.showBannerAd();
        }
        else if (Laya.Browser.onVVMiniGame) {
            SGAD.showCustomAd();
        }
    }
    static backToHome(cb?: Function) {
        if (!Laya.Browser.onWeiXin && !Laya.Browser.onQGMiniGame && !Laya.Browser.onVVMiniGame) { cb && cb(); return; }
        SGAD.hideBannerAd();
        if (Laya.Browser.onWeiXin) {
            // SGAD.hideBannerAd();
            SGAD.visibleSideGridAd(false);
            this.showRemen(RemenIndex.Remen_Finish, () => {
                cb && cb();
                this.gameCount++;
            })
        }
        else if (Laya.Browser.onQGMiniGame) {
            this.showNativeUI(true, "_OPPO", cb);
        }
        else if (Laya.Browser.onVVMiniGame) {
            cb && cb();
        }
    }

    static addToDesk() {
        if (!Laya.Browser.onWeiXin && !Laya.Browser.onQGMiniGame && !Laya.Browser.onVVMiniGame) { return; }
        Laya.Browser.window['qg'].hasShortcutInstalled({
            success: () => {
                // 判断图标未存在时，创建图标
                Laya.Browser.window['qg'].installShortcut({
                    success: function () {
                        // 执行用户创建图标奖励
                    }
                })
            }
        })
    }

    static vibrateCB() {
        this.isVibrate = !this.isVibrate
    }

    //热门页
    private static showRemen(index: RemenIndex, cb?: Function, isMust: boolean = false) {
        let v = false
        switch (index) {
            case RemenIndex.Remen_Loading:
                v = SGConfig.data.front_remen_loading_switch
                break
            case RemenIndex.Remen_Start:
                v = SGConfig.data.front_remen_start_switch && this.gameCount >= SGConfig.data.front_remen_start_gameCount
                break
            case RemenIndex.Remen_Over:
                v = SGConfig.data.front_remen_over_switch && this.gameCount >= SGConfig.data.front_remen_over_gameCount
                break
            case RemenIndex.Remen_Finish:
                v = SGConfig.data.front_remen_finish_switch && this.gameCount >= SGConfig.data.front_remen_finish_gameCount
                break
        }
        if (v || isMust) {
            Laya.Scene.open(SceneType.SGRemen, false, { ccb: cb, index: index })
        } else {
            cb && cb()
        }
    }

    //首页
    // private static visibleHomeUI_Wx(v = true) {
    //     if (v)
    //         Laya.Scene.open(SceneType.SGHomeUI, false)
    //     else
    //         Laya.Scene.close(SceneType.SGHomeUI)
    // }



    static visiblePrivacyUI(v: boolean, path: string = "") {
        if (v) Laya.Scene.open('SGScene' + path + '/SGPrivacy.scene', false)
        else Laya.Scene.close('SGScene' + path + '/SGPrivacy.scene')
    }
    private static visibleLoadingUI(v: boolean, path: string = "") {
        if (v) Laya.Scene.open('SGScene' + path + '/SGLoading.scene', false)
        else Laya.Scene.close('SGScene' + path + '/SGLoading.scene')
    }
    private static visibleHomeUI(v: boolean, path: string = "") {
        if (v) Laya.Scene.open('SGScene' + path + '/SGHome.scene', false)
        else Laya.Scene.close('SGScene' + path + '/SGHome.scene')
    }
    private static showNativeUI(v: boolean = true, path: string = "", cb?: Function) {
        SGAD.showCustomAd()
        cb && cb();
        // if (v) Laya.Scene.open('SGScene' + path + '/SGCustom.scene', false, { ccb: cb })
        // else Laya.Scene.close('SGScene' + path + '/SGCustom.scene')
    }


    public static addClickEvent(btn: Laya.Sprite, caller: any, callBack: Function, param?: any[], isScale?: boolean) {
        btn.offAllCaller(caller);

        if (btn instanceof Laya.Button && !isScale) {
            let callback = (event) => {
                if (callBack) callBack.call(caller, event);
            }
            btn.on(Laya.Event.CLICK, caller, callback, [param]);
        }
        else {
            let scaleTime = 60;
            let wRatio = 1;
            let scaleX = btn.scaleX * wRatio;
            let scaleY = btn.scaleY * wRatio;
            let size = 0.9 * wRatio;

            let isPressed = false;
            let cbDown = (event) => {
                isPressed = true;
                Laya.Tween.to(btn, { scaleX: size, scaleY: size }, scaleTime);
            }
            btn.on(Laya.Event.MOUSE_DOWN, caller, cbDown, [param]);

            let cbUp = (event) => {
                if (isPressed == false) return;
                isPressed = false;
                Laya.Tween.to(btn, { scaleX: scaleX, scaleY: scaleY }, scaleTime, null, new Laya.Handler(caller, () => {
                    if (callBack) callBack.call(caller, event);
                }));
            }
            btn.on(Laya.Event.MOUSE_UP, caller, cbUp, [param]);

            let cbOut = (event) => {
                Laya.Tween.to(btn, { scaleX: scaleX, scaleY: scaleY }, scaleTime);
            }
            btn.on(Laya.Event.MOUSE_OUT, caller, cbOut, [param]);
        }
    }
}

export enum SceneType {
    SGRemen = "SGScene/SGRemen.scene",
    SGHomeUI = "SGScene/SGHome.scene"
}

export enum RemenIndex {
    Remen_Loading,
    Remen_Start,
    Remen_Over,
    Remen_Finish
}