import SoundMgr from "../SoundMgr";
import Utility from "../Utility";

export default class PhysicsCrushAnim extends Laya.Script {
    constructor() {
        super()
    }
    Displaye: Laya.Sprite = null;
    isWord: boolean = false;

    onAwake() {
        this.Displaye = this.owner as Laya.Sprite
    }


    bIndex_1: number = 0;
    bemonve_1(ow: Laya.Sprite3D, pointArr: Laya.Vector3[], region: any = null, success: Function = null,) {
        let endpos = pointArr[this.bIndex_1];
        if (this.isWord) {
            // console.log(">>>>>", endpos)
            Utility.WordPositionTween(ow, endpos, 10, Laya.Ease.linearIn, this, () => {
                this.bIndex_1++;
                if (this.bIndex_1 >= 50) {
                    if (region == null) return;
                    if (success == null) return;
                    success.apply(region);
                    return;
                }
                this.bemonve_1(ow, pointArr, region, success);
            });
        } else {
            Utility.LocalPositionTween(ow, endpos, 10, Laya.Ease.linearIn, this, () => {
                this.bIndex_1++;
                if (this.bIndex_1 >= 50) {
                    if (region == null) return;
                    if (success == null) return;
                    success.apply(region);
                    return;
                }
                this.bemonve_1(ow, pointArr, region, success);
            });
        }
    }
}