    /** 随机数 支持浮点数传入传出 */
    public static GetRandomRangeNum(maxNum, minNum, decimalNum): number {
        var max = 0, min = 0;
        minNum <= maxNum ? (min = minNum, max = maxNum) : (min = maxNum, max = minNum);
        switch (arguments.length) {
            case 1:
                return Math.floor(Math.random() * (max + 1));
                break;
            case 2:
                return Math.floor(Math.random() * (max - min + 1) + min);
                break;
            case 3:
                return parseFloat((Math.random() * (max - min) + min).toFixed(decimalNum));
                break;
            default:
                return Math.random();
                break;
        }
    }

    //计算包围盒覆盖面积点
    static GetBoxPoint(p1, p2, p1length, p2length): Laya.Point[] {
        var _arrPoint = [];
        for (let _p11 = 0; _p11 < p1length * 2; _p11++) {
            for (let _p22 = 0; _p22 < p2length * 2; _p22++) {
                var _point = new Laya.Point(p1 - p1length + _p11, p2 - p2length + _p22);
                _arrPoint.push(_point);
            }
        }
        return _arrPoint;
    }

    /**倒计时处理 列：传入60 返回 01:00， time 参数最大60 * 59 */
    public static countDownTimeHandler(time: number): string {
        if (time <= 0) return "00:00";
        let hm: any = Math.floor(time / 60);
        hm = (hm + "").length == 1 ? "0" + hm : hm;
        let second: any = Math.ceil(time % 60);
        second = (second + "").length == 1 ? "0" + second : second;
        return hm + ":" + second;
    }

    /**货币格式化 */
    public static formatCurrencyWithSuffix(num: number): string {
        let thresh = 1000;
        if (Math.abs(num) < thresh) {
            return num.toFixed(2) + '';
        }
        let units = ['K', 'M', 'B', 'T'];
        let u = -1;
        let r = 10 ** Math.floor(Math.log10(Math.abs(num)) / 3) * thresh;
        while (u < units.length && r >= thresh) {
            u++;
            r /= thresh;
        }
        return r.toFixed(2) + units[u];
    }


    /**抽奖概率算法
     * arr1 中奖奖励数组            arr1 = ['一等奖', '二等奖', '三等奖', '四等奖']; // 奖池
     * arr2 各个奖励中奖概率数组     arr2 = [0.5, 0.5, 0, 0]; // 概率
     */
    public static randomProbability(arr1, arr2) {
        var sum = 0,
            factor = 0,
            random = Math.random();
        for (var i = arr2.length - 1; i >= 0; i--) {
            sum += arr2[i]; // 统计概率总和
        };
        random *= sum; // 生成概率随机数
        for (var i = arr2.length - 1; i >= 0; i--) {
            factor += arr2[i];
            if (random <= factor) return arr1[i];
        };
        return null;
    }

