    /**按照名字递归获取下面某个节点 */
    static FindChildByName(name: string, parent: Laya.Node): Laya.Node {
        // 递归函数
        let FindRecursion = (iParent: Laya.Node): Laya.Node => {
            if (iParent.name == name) return iParent;
            let node = null;
            for (let i = 0; i < iParent.numChildren; i++) {
                if (iParent.getChildAt(i).name == name) {
                    node = iParent.getChildAt(i);
                    break;
                }
                if (iParent.getChildAt(i).numChildren) {
                    node = FindRecursion(iParent.getChildAt(i));
                    if (node) break;
                }
            }
            return node;
        };
        // 初始遍历
        if (parent.name == name) return parent;
        for (let i = 0; i < parent.numChildren; i++) {
            let result = FindRecursion(parent.getChildAt(i));
            if (result) return result;
        }
        return null;
    }

