    /**获取两个点（三维点）的距离 */
    static GetPointV3Distance(point1: Laya.Vector3, point2: Laya.Vector3): number {
        let x = Math.abs(point1.x - point2.x);
        let y = Math.abs(point1.y - point2.y);
        let z = Math.abs(point1.z - point2.z);
        let distance = Math.sqrt(Math.pow(x, 2) + Math.pow(y, 2) + Math.pow(z, 2));
        return distance;
    }

    /**计算两个向量的差 */
    static SubVector(_v1: Laya.Vector3, _v2: Laya.Vector3) {
        let _v = new Laya.Vector3(0, 0, 0);
        Laya.Vector3.subtract(_v1, _v2, _v);
        return _v;
    }

    /**
     * 获取两点之间的距离
     * @param a 点a
     * @param b 点b
     */
    public static calcDistance(a, b) {
        return Math.sqrt(Math.pow(a.x - b.x, 2) + Math.pow(a.y - b.y, 2));
    }

    public static getWorldDis(a: Laya.Sprite3D, b: Laya.Sprite3D) {
        let pA: Laya.Vector3 = a.transform.position.clone()
        let pB: Laya.Vector3 = b.transform.position.clone()

        return Laya.Vector3.distance(pA, pB)
    }

    /**
     * 获取A指向B的向量
     * @param A A对象
     * @param B B对象
     * @param normalize 默认单位化向量
     */
    public static getDirectionAToB(A: Laya.Sprite3D, B: Laya.Sprite3D, normalize: boolean = true) {
        let pA: Laya.Vector3 = A.transform.position.clone()
        let pB: Laya.Vector3 = B.transform.position.clone()

        let dir = new Laya.Vector3(0, 0, 0)
        Laya.Vector3.subtract(pB, pA, dir)
        if (normalize)
            Laya.Vector3.normalize(dir, dir)
        return dir
    }

