import SGMgr from "../SGMgr"


export default class SGPrivacy_OPPO extends Laya.Scene {
    constructor() {
        super()
    }

    btnAgree: Laya.Image
    btnDisagree: Laya.Image
    scroll: Laya.Panel

    onClosed(type?: string): void {

    }

    onOpened(param: any): void {
        this.scroll.vScrollBarSkin = ''
        localStorage.setItem('privacy', '1')
        this.size(Laya.stage.displayWidth, Laya.stage.displayHeight)
        SGMgr.addClickEvent(this.btnAgree, this, this.agreeCB)
        SGMgr.addClickEvent(this.btnDisagree, this, this.disagreeCB)
    }

    agreeCB() {
        this.close()
    }

    disagreeCB() {
        if (!Laya.Browser.onQGMiniGame) return
        Laya.Browser.window['qg'].exitApplication({
            complete: (res) => {
                console.log(res)
            }
        })
    }
}