    baseAddNum: number = 0;
    baseAddNumMax: number = 0.5;
    isInSprint: boolean = false;
    onAddBaseSpeedCB() {
        if (this.isDie) return;
        console.log("下坡加速")
        GameLogic.Share._cameraCrl.SpeedLine_1.active = false;
        GameLogic.Share._cameraCrl.SpeedLine_1.active = true;
        this.isInSprint = true;
        SoundMgr.instance.playSoundEffect('BoosterSpeed');
        this.Eff.getChildByName("FX_GetGold_4_1").active = true;
        this.Eff.getChildAt(0).active = true;
        this.baseAddNum = this.baseAddNumMax;
        // if (this.isAddSpeedBtnClick) {
        Laya.timer.clear(this, this.AddSpeedLoop);
        Laya.timer.loop(10, this, this.AddSpeedLoop);
        // } else { }
        return;
        Laya.timer.clear(this, this.AddBaseSpeedLoop);
        Laya.timer.clear(this, this.DelBaseSpeedLoop);
        Laya.timer.loop(10, this, this.AddBaseSpeedLoop);
    }
    AddBaseSpeedLoop() {
        if (this.isDie) return;
        if (this.baseAddNum >= this.baseAddNumMax) {
            this.baseAddNum = this.baseAddNumMax;
            // Laya.timer.clear(this, this.DelSpeedLoop);
            return;
        }
        // console.log(GameLogic.Share._ctr.speedFactor);
        this.baseAddNum += 0.02;
    }

    onDelBaseSpeedCB() {
        // return;
        if (this.isInRocketSpeed) return;
        GameLogic.Share._cameraCrl.SpeedLine_1.active = false;
        console.log("下坡减速")
        this.isInSprint = false;
        this.baseAddNum = 0;
        this.Eff.getChildByName("FX_GetGold_4_1").active = false;
        if (this.isAddSpeedBtnClick) {
            Laya.timer.clear(this, this.AddSpeedLoop);
            Laya.timer.loop(10, this, this.AddSpeedLoop);
        } else { }
        return
        Laya.timer.clear(this, this.AddBaseSpeedLoop);
        Laya.timer.clear(this, this.DelBaseSpeedLoop);
        Laya.timer.loop(10, this, this.DelBaseSpeedLoop);
    }
    DelBaseSpeedLoop() {
        if (this.baseAddNum <= 0) {
            this.baseAddNum = 0;
            // Laya.timer.clear(this, this.DelSpeedLoop);
            return;
        }
        // console.log(GameLogic.Share._ctr.speedFactor);
        this.baseAddNum -= 0.005;
    }

    GameOver(isWin: boolean) {
        console.log("游戏结束");
        Laya.timer.clearAll(this);
        this.Eff.getChildAt(0).active = false;
        this.isDie = true;
        GameManager.Instance.AllPlayerRank.push(this.Display);
        GameLogic.Share._ctr.speedFactor = GameLogic.Share.speedFactor = 0;
        CameraCrl.Instance.CameraState = CameraState.GameStart;
        GameLogic.Share.gameOver(isWin);
        GameLogic.Share._cameraCrl.SpeedLine_1.active = false;
        if (isWin) {
            GameLogic.Share._cameraCrl.FX_Fire1.active = false;
            GameLogic.Share._cameraCrl.FX_Fire1.active = true;
        }
    }

    baseSpeed: number = 0.2;
    maxAddSpeed: number = 0.3;
    isAddSpeedBtnClick: boolean = false;

    onInitBaseSpeed() {
        this.maxAddSpeed = this.baseSpeed + PlayerDataMgr.getPlayerData().speedTopLv[this._poolId] * 0.1 * 0.2;
        this.baseAddNumMax = this.maxAddSpeed + PlayerDataMgr.getPlayerData().rocketLv[this._poolId] * 0.1 * 0.2;
    }
    // endSpeed: number = 0.2;
