    //#endregion

    onInInvincibleTime(isBuy: boolean = false) {
        this.isInvincible = true;
        this.Eff.getChildAt(6).active = true;
        if (isBuy) return;
        Laya.timer.once(2000, this, this.onOutInvincibleTime)
    }

    onOutInvincibleTime() {
        this.isInvincible = false;
        this.Eff.getChildAt(6).active = false;
    }

    isInvincible: boolean = false;
    isDie: boolean = false;
    onUpdate(): void {
        if (this.isDie) return;
        // console.log(GameLogic.Share.speedFactor);
        // if (!GameLogic.Share.isStartGame) return;
        // this.onMove();
        // this.onRotation();
        this.onRoateNodeMove();
    }
