import GameLogic from "../Crl/GameLogic"
import { GameManager } from "../Crl/GameManager"
import PlayerDataMgr from "../Libs/PlayerDataMgr"
import EventMgr, { EventType } from "../Mod/EventManager"
import SoundMgr from "../Mod/SoundMgr"
import Utility from "../Mod/Utility"
import SGAD from "../SGSDK/SGAD"
import SGMgr from "../SGSDK/SGMgr"

export default class FreeSkinUI extends Laya.Scene {
    constructor() {
        super()
    }

    PoolBox: Laya.Box;
    BoxBg: Laya.Image;
    Icon: Laya.Image;
    SpeedLevelLabel: Laya.Label;
    SpeedTopLevelLabel: Laya.Label;
    RotSpeedLevelLabel: Laya.Label;
    RocketLevelLabel: Laya.Label;
    isCanShow: boolean = false;
    CloseBtn: Laya.Image;
    VideoBtn: Laya.Image;

    startIndex: number = 0;
    randomIndex: number = 0;

    onOpened() {
        this.size(Laya.stage.displayWidth, Laya.stage.displayHeight);

        this.randomIndex = 0;
        let random = Utility.GetRandom(1, 2);
        this.isCanShow = false;
        // if (random == 0) {
        this.InitFreeSkinUI(true);
        // } else {
        //     this.InitFreeSkinUI(false);
        // }
        // let random = Utility.GetRandom(1, 2, true);
        // if (random == 1) {
        //     // this.WagonBox.active = true;
        //     this.initWagonBox();
        // } else {
        //     // this.ManBox.active = true;
        //     this.initManBox();
        // }


        Utility.addClickEvent(this.CloseBtn, this, this.closeCB);
        Utility.addClickEvent(this.VideoBtn, this, this.adBtnCBWagon);

        SGAD.showBannerAd();
    }

    freePoolIndex: number = 0;
    initWagonBox() {
        this.BoxBg.scale(0, 0);
        let random = this.startIndex;
        this.freePoolIndex = random;
        this.Icon.skin = 'startUI/skin/pools_' + (random + 1) + '.png';
        this.SpeedLevelLabel.text = "+" + GameManager.Instance._pool_data[random].Acc[0].toString();
        this.SpeedTopLevelLabel.text = "+" + GameManager.Instance._pool_data[random].Top[0].toString();
        this.RotSpeedLevelLabel.text = "+" + GameManager.Instance._pool_data[random].Rotate[0].toString();
        this.RocketLevelLabel.text = "+" + GameManager.Instance._pool_data[random].Rocket[0].toString();
        // this.WagonBox.visible = true;
        // this.ManBox.visible = false;
        SoundMgr.instance.playSoundEffect("UIShow");
        Laya.Tween.to(this.BoxBg, { scaleX: 1, scaleY: 1 }, 300, Laya.Ease.backOut)
    }
    // freeManSkinIndex: number = 0;
    // initManBox() {
    //     let random = this.startIndex;
    //     this.freeManSkinIndex = random;
    //     this.ManIcon.skin = 'skinUI/Man/Man_' + (random + 1) + '.png';
    //     this.ManBox.visible = true;
    //     this.WagonBox.visible = false;
    // }


    InitFreeSkinUI(isWagon: boolean) {
        if (this.randomIndex >= 2) {
            // console.log("this.randomIndex  大于2")
            GameLogic.Share.CloseFreeSkin();
            return;
        }
        this.randomIndex++;
        if (isWagon) {
            for (let i = 0; i < PlayerDataMgr.getPlayerData().poolArr.length; i++) {
                if (PlayerDataMgr.getPlayerData().poolArr[i] == 0) {
                    this.isCanShow = true;
                    this.startIndex = i;
                    // console.log(this.startIndex);
                    break;
                }
            }
            if (this.isCanShow) {
                this.initWagonBox();
            }
            else {
                // this.InitFreeSkinUI(false);
                GameLogic.Share.CloseFreeSkin();
            }
        } else {
            // for (let i = 0; i < PlayerDataMgr.getPlayerData().ManskinArr.length; i++) {
            //     if (PlayerDataMgr.getPlayerData().ManskinArr[i] == 0) {
            //         this.isCanShow = true;
            //         this.startIndex = i;
            //         break;
            //     }
            // }
            // if (this.isCanShow) {
            //     this.initManBox();
            // }
            // else {
            //     this.InitFreeSkinUI(true);
            // }
        }
    }

    onClosed() {
        SGAD.hideBannerAd();
    }

    adBtnCBWagon() {
        SGAD.showVideoAd(() => {
            PlayerDataMgr.getPlayerData().poolFreeId = this.freePoolIndex;
            PlayerDataMgr.setPlayerData();
            EventMgr.SendEvent(EventType.ChangeSkin, []);
            this.closeCB();
        })
    }

    adBtnCBMan() {
        SGAD.showVideoAd(() => {
            // PlayerDataMgr.getPlayerData().ManFreeSkinId = this.freeManSkinIndex;
            PlayerDataMgr.setPlayerData();
            // EventMgr.SendEvent(EventType.ChangeManSkin);
            this.closeCB();
        })
    }

    closeCB() {
        SoundMgr.instance.playSoundEffect("UIShowDown");
        Laya.Tween.to(this.BoxBg, { scaleX: 0, scaleY: 0 }, 300, Laya.Ease.backIn, Laya.Handler.create(this, () => {
            GameLogic.Share.CloseFreeSkin();
        }))
        // GameLogic.Share.restartGame();
        // Laya.Scene.open('MyScenes/GameUI.scene');
    }
}
