import GameLogic from "../../Crl/GameLogic";
import PlayerDataMgr from "../../Libs/PlayerDataMgr"
import EventMgr, { EventType } from "../EventManager";
import Utility from "../Utility";

export default class CoinNodeUI extends Laya.Script {
    constructor() {
        super()
    }

    Display: Laya.Sprite = null

    getCoinBtn: Laya.Image;
    coinLabel: Laya.FontClip;

    onAwake() {
        this.Display = this.owner as Laya.Sprite;
        this.getCoinBtn = this.Display.getChildAt(1) as Laya.Image;
        this.coinLabel = this.Display.getChildAt(2) as Laya.FontClip;

        Utility.addClickEvent(this.getCoinBtn, this, this.onGetCoinBtnCB);
        EventMgr.AddListener(EventType.UpdateAllCoinNodeUI, this, this.onUpdateUI);
    }

    onEnable(): void {
        this.onUpdateUI();
    }

    onDisable(): void {
        EventMgr.Off(EventType.UpdateAllCoinNodeUI, this, this.onUpdateUI);
    }

    onGetCoinBtnCB() {
        GameLogic.Share.showGetCoinUI();
        // PlayerDataMgr.addPlayerStrength();
        // this.onUpdateUI();
    }

    onUpdateUI() {
        // this.CoinLabel.text = Utility.formatCurrencyWithSuffix(PlayerDataMgr.getPlayerData().coin);
        this.coinLabel.value = PlayerDataMgr.getPlayerData().coin.toString();
    }

    onUpdate() {
        this.onUpdateUI();
    }
}