import PlayerDataMgr from "../../Libs/PlayerDataMgr";
import PhysicsCrushAnim from "../../Mod/3D/PhysicsCrushAnim";
import Utility from "../../Mod/Utility";
import GameLogic from "../GameLogic";
import PlayerController from "../role/PlayerController";

export default class MESBombeController extends Laya.Script3D {

    Display: Laya.Sprite3D;
    MES_Bombe: Laya.Sprite3D;
    FX_Bomb2: Laya.Sprite3D;

    onAwake() {
        this.Display = this.owner as Laya.Sprite3D;
        this.MES_Bombe = Utility.FindChildByName("MES_Bombe", this.Display) as Laya.Sprite3D;
        this.FX_Bomb2 = Utility.FindChildByName("FX_Bomb2", this.Display) as Laya.Sprite3D;
    }

    // onInitAnim() {
    //     Ball
    // }

    onPushBall(isHide: boolean = true) {
        if (!isHide) return;
        this.Display.getComponent(Laya.PhysicsCollider).enabled = false;
        this.FX_Bomb2.active = true;
        this.MES_Bombe.active = false;
    }

    onUpdate(): void {
        // this.MES_DontMove_1.transform.localRotationEulerY++;
    }
}