import PlayerDataMgr from "../../Libs/PlayerDataMgr"

export default class UpdateGrade extends Laya.Script {
    constructor() {
        super()
    }

    myOwner: Laya.Label = null

    onAwake() {
        this.myOwner = this.owner as Laya.Label
    }

    onUpdate() {
        this.myOwner.text = PlayerDataMgr.getPlayerData().grade.toString()
    }
}