import SoundMgr from "../../Mod/SoundMgr";
import BNowlingBottleController from "../obj/BNowlingBottleController";
import BallController from "../obj/BallController";
import CameraCrl from "../CameraCrl";
import CoinController from "../obj/CoinController";
import DiamondController from "../obj/DiamondController";
import { CameraState } from "../Enum";
import GameLogic from "../GameLogic";
import PlayerController from "./PlayerController";
import SwimmingPoolController_1 from "../obj/SwimmingPoolController_1";
import TowerController from "../obj/TowerController";
import MESRailStopController from "../obj/MESRailStopController";
import MESDontMoveController from "../obj/MESDontMoveController";
import MESLongObstacleHorizontaleController from "../obj/MESLongObstacleHorizontaleController";
import MESBombeController from "../obj/MESBombeController";
import AIController from "./AIController";

export default class AIRoateNodeController extends Laya.Script3D {
    constructor() {
        super();
    }

    _playerCtr: AIController = null;

    onAwake(): void {

    }


    onTriggerEnter(other: Laya.PhysicsComponent): void {
        // if (this._playerCtr.isDie) return;
        let ow = other.owner as Laya.Sprite3D;
        let ctr;
        let childFart;
        // console.log(":!!!!!!!!!!", ow.name);;
        switch (ow.name) {
            case "SpeedChangePathMove":
                GameLogic.Share.PushMovePath(GameLogic.Share._movePath, GameLogic.Share._ctr, GameLogic.Share._player);
                break;
            case "AddSpeedBox":
                // this._playerCtr.onAddBaseSpeedCB();
                break;
            case "DelSpeedBox":
                // this._playerCtr.onDelBaseSpeedCB();
                break;
            case "DownInWaterStartBox":
                break;
            case "DownInWaterBox":
                // SoundMgr.instance.playSoundEffect("SplashImpact");
                // ow.getComponent(Laya.PhysicsCollider).enable = false;
                // ow.active = false;
                // this._playerCtr.onDownInWaterBoxCB();
                break;
            case "OutWaterBox":
                // SoundMgr.instance.playSoundEffect("Wake");
                // ow.getComponent(Laya.PhysicsCollider).enable = false;
                // ow.active = false;
                // this._playerCtr.onOutWaterBoxCB();
                break;
            case "Ball_1":
                ctr = ow.getComponent(BallController);
                ctr.onPushBall(false);
                this._playerCtr.onBall_1CB();
                break;
            case "SwimmingPool_1":
                ctr = ow.getComponent(SwimmingPoolController_1);
                ctr.onBreakTopBox();
                break;
            case "Tower_1":
                ctr = ow.getComponent(TowerController);
                ctr.onBreakTopBox(false);
                this._playerCtr.onTower_1CB();
                break;
            case "BNowlingBottle_All":
                ctr = ow.getComponent(BNowlingBottleController);
                ctr.onBreakTopBox(false);
                this._playerCtr.onBNowlingBottle_AllCB();
                break;
            case "MES_RailStop":
                ctr = ow.getComponent(MESRailStopController);
                ctr.onPushBall(false);
                this._playerCtr.onMES_RailStopCB();
                break;
            case "MES_DontMove":
                ctr = ow.getComponent(MESDontMoveController);
                ctr.onPushBall(false);
                this._playerCtr.onMES_RailStopCB();
                break;
            case "MES_LongObstacle_Horizontale":
                ctr = ow.getComponent(MESLongObstacleHorizontaleController);
                ctr.onPushBall(false);
                this._playerCtr.onMES_RailStopCB();
                break;
            case "MES_Bombe":
                ctr = ow.getComponent(MESBombeController);
                ctr.onPushBall(false);
                this._playerCtr.onMES_RailStopCB();
                break;
        }
    }
}