import { Obstacle } from "../Crl/Enum";


export class PreFabCtr {
    private static _instance: PreFabCtr;
    Path: string = "";
    ShowScene: Laya.Sprite3D;
    GameScene: Laya.Sprite3D;
    SkinMaterial: Laya.BlinnPhongMaterial[];
    WeaponMaterial: Laya.BlinnPhongMaterial[];
    path = "assets/"; // OV使用路径
    // AssetsPath = {
    //     ScenePath: PreFabCtr.instance.path + "LayaScene_GameScene_Prefabe/Conventional/GameScene_Prefabe.lh",
    //     JsonInfo: PreFabCtr.instance.path + "Level0",
    //     PlayerPath: PreFabCtr.instance.path + "LayaScene_GameScene_Prefabe/Conventional/GameScene_Prefabe.lh",
    // }

    static get instance(): PreFabCtr {
        if (!this._instance) {
            this._instance = new PreFabCtr();
        }
        return this._instance;
    };
    constructor() {
        if (Laya.Browser.onMiniGame) {
            this.Path = Laya.Browser.window.wx.env.USER_DATA_PATH + "/cache/";
        }
    }
    getGameScene(name: string, isClone: boolean = true) {
        let obj = this.GameScene.getChildByName(name) as Laya.Sprite3D;
        if (isClone) {
            obj = obj.clone() as Laya.Sprite3D;
        }
        return obj;
    }
    getShowScene(name: string, isClone: boolean = true) {
        let obj = this.ShowScene.getChildByName(name) as Laya.Sprite3D;
        if (isClone) {
            obj = obj.clone() as Laya.Sprite3D;
        }
        return obj;
    }
    getPreFabClone(name: string, isClone: boolean = true) {
        let obj = this.ShowScene.getChildByName("AllPrefabe").getChildByName(name) as Laya.Sprite3D;
        if (isClone) {
            obj = obj.clone() as Laya.Sprite3D;
        }
        return obj;
    }
    getRoleClone(name: string, isClone: boolean = true) {
        let obj = this.ShowScene.getChildByName(name) as Laya.Sprite3D;
        // let obj = roleClone.getChildByName(name) as Laya.Sprite3D;
        if (isClone) {
            obj = obj.clone() as Laya.Sprite3D;
        }
        return obj;
    }
    getEffClone(name: string, isClone: boolean = true) {
        let effClone = this.ShowScene.getChildByName(Obstacle.EffClone);
        let obj = effClone.getChildByName(name) as Laya.Sprite3D;
        if (isClone) {
            obj = obj.clone() as Laya.Sprite3D;
        }
        return obj;
    }
    public ChangeWeapon(target: Laya.Sprite3D, weaponId: number) {
        let weapon = target as Laya.MeshSprite3D;
        weapon.meshRenderer.material = PreFabCtr.instance.WeaponMaterial[weaponId];
    }
    public SkinColor = [
        new Laya.Vector4(0.8396226, 0, 0, 1),
        new Laya.Vector4(0.9215686, 1, 1, 1),
        new Laya.Vector4(0, 0.3647059, 1, 1),
        new Laya.Vector4(0.3254902, 0.3411765, 0.3607843, 1),
        new Laya.Vector4(1, 0.6901961, 0, 1),
        new Laya.Vector4(0, 0.7411765, 0.02745098, 1),
    ]
}