import SGAD from "../SGAD"
import SGMgr from "../SGMgr"


export default class SGHome_OPPO extends Laya.Scene {
    constructor() {
        super()
    }

    btnDesk: Laya.Image
    btnMore: Laya.Image
    btnPrivacy: Laya.Image
    btnVibrate: Laya.Image

    onClosed(type?: string): void {

    }

    onOpened(param: any): void {
        this.size(Laya.stage.displayWidth, Laya.stage.displayHeight)
        SGMgr.addClickEvent(this.btnDesk, this, this.btnDeskCB)
        SGMgr.addClickEvent(this.btnMore, this, this.btnMoreCB)
        SGMgr.addClickEvent(this.btnPrivacy, this, this.btnPrivacyCB)
        SGMgr.addClickEvent(this.btnVibrate, this, this.btnVibrateCB)
    }

    btnDeskCB() {
        SGMgr.addToDesk()
    }

    btnMoreCB() {
        SGAD.showGamePortalAd()
    }

    btnPrivacyCB() {
        SGMgr.visiblePrivacyUI(true,"_OPPO")
    }

    btnVibrateCB() {
        SGMgr.vibrateCB();
        (this.btnVibrate.getChildByName('sp1') as Laya.Image).visible = SGMgr.isVibrate;
        (this.btnVibrate.getChildByName('sp2') as Laya.Image).visible = !SGMgr.isVibrate;
    }
}