export default class ShakeLoop extends Laya.Script {
    constructor() {
        super()
    }
    myOwnwer: Laya.Image = null;
    isInAwakeShake: boolean = true;

    onAwake() {
        this.myOwnwer = this.owner as Laya.Image;
        Laya.timer.clearAll(this);
        Laya.Tween.clearAll(this.myOwnwer);
        this.Shake();
    }

    Shake() {
        Laya.Tween.to(this.myOwnwer, { rotation: -10 }, 100, Laya.Ease.linearIn, Laya.Handler.create(this, () => {
            Laya.Tween.to(this.myOwnwer, { rotation: 10 }, 100, Laya.Ease.linearIn, Laya.Handler.create(this, () => {
                Laya.Tween.to(this.myOwnwer, { rotation: 0 }, 100, Laya.Ease.linearIn, Laya.Handler.create(this, () => {
                    let func = () => {
                        this.Shake();
                    }
                    this.myOwnwer.timerOnce(1000, this, func);
                }));
            }));
        }));
    }

    onUpdate() {
    }
}