export default class JellyShakeLoop extends Laya.Script {
    constructor() {
        super()
    }
    /** @prop {name:isVOrH, tips:"竖着弹--true || 横着弹--false", type:Bool, default:true}*/
    public isVOrH: boolean = true;
    myOwnwer: Laya.Image = null;

    onAwake() {
        this.myOwnwer = this.owner as Laya.Image;
        Laya.timer.clearAll(this);
        Laya.Tween.clearAll(this.myOwnwer);
        this.Shake();
    }

    Shake() {
        if (this.isVOrH) {
            Laya.Tween.to(this.myOwnwer, { scaleY: 0.5 }, 100, Laya.Ease.linearIn, Laya.Handler.create(this, () => {
                Laya.Tween.to(this.myOwnwer, { scaleY: 1.2 }, 100, Laya.Ease.linearIn, Laya.Handler.create(this, () => {
                    Laya.Tween.to(this.myOwnwer, { scaleY: 1 }, 100, Laya.Ease.bounceOut, Laya.Handler.create(this, () => {
                        let func = () => {
                            this.Shake();
                        }
                        this.myOwnwer.timerOnce(1000, this, func);
                    }));
                }));
            }));
        } else {
            Laya.Tween.to(this.myOwnwer, { scaleX: 0.5 }, 100, Laya.Ease.linearIn, Laya.Handler.create(this, () => {
                Laya.Tween.to(this.myOwnwer, { scaleX: 1.2 }, 100, Laya.Ease.linearIn, Laya.Handler.create(this, () => {
                    Laya.Tween.to(this.myOwnwer, { scaleX: 1 }, 100, Laya.Ease.bounceOut, Laya.Handler.create(this, () => {
                        let func = () => {
                            this.Shake();
                        }
                        this.myOwnwer.timerOnce(1000, this, func);
                    }));
                }));
            }));
        }
    }

    onUpdate() {
    }
}