import PlayerDataMgr, { PlayerData } from "../../Libs/PlayerDataMgr";
import EventMgr, { EventType } from "../../Mod/EventManager";
import SoundMgr from "../../Mod/SoundMgr";
import Utility from "../../Mod/Utility";
import SGConfig from "../../SGSDK/SGConfig";
import BuildPreFabe from "../BuildPreFabe";
import CameraCrl from "../CameraCrl";
import { AnimName, CameraState } from "../Enum";
import GameLogic from "../GameLogic";
import { GameManager } from "../GameManager";
import CoinController from "../obj/CoinController";
import DiamondController from "../obj/DiamondController";
import BallController from "../obj/BallController";
import SwimmingPoolController_1 from "../obj/SwimmingPoolController_1";
import BNowlingBottleController from "../obj/BNowlingBottleController";
import TowerController from "../obj/TowerController";
import AIRoateNodeController from "./AIRoateNodeController";

export default class AIController extends Laya.Script3D {
    constructor() {
        super();
    }

    Display: Laya.Sprite3D = new Laya.Sprite3D;
    PathSprite: Laya.Sprite3D;
    // AddSpeedEff: Laya.Sprite3D;

    Roles: Laya.Sprite3D;
    RoateNode: Laya.Sprite3D;
    Role: Laya.Animator;
    Skate: Laya.Sprite3D;
    Eff: Laya.Sprite3D;
    _index: number;

    _rigbodyCtr: AIRoateNodeController = null;

    onAwake(): void {
        this.Display = this.owner as Laya.Sprite3D;
        this.Roles = Utility.FindChildByName("Roles", this.Display) as Laya.Sprite3D;
        this.RoateNode = Utility.FindChildByName("RoateNode", this.Display) as Laya.Sprite3D;
        this.Skate = Utility.FindChildByName("Skate", this.Display) as Laya.Sprite3D;
        this.Eff = Utility.FindChildByName("Eff", this.Display) as Laya.Sprite3D;
        // console.log(this.Display);
        this._rigbodyCtr = this.RoateNode.addComponent(AIRoateNodeController);
        this._rigbodyCtr._playerCtr = this;
        // this.init();
        this.onInitEff();
        // Laya.stage.on(Laya.Event.KEY_DOWN, this, this.onKeyDown);
        // console.log("??????")
        EventMgr.AddListener(EventType.AIStartGame, this, this.onStartGame)
        EventMgr.AddListener(EventType.AIInit, this, this.init);
        EventMgr.AddListener(EventType.CleanAllSpeed, this, this.onCleanAllSpeed);
        EventMgr.AddListener(EventType.onStopGame, this, this.onStopGame);
        // this.init();
    }
    onEnable(): void {
        this.init();
    }

    onDisable(): void {
    }

    onDestroy(): void {
        EventMgr.OffAllCaller(this);
    }

    PlayerGameTime: number = 180;
    init() {
        this.onChangeSkin();
        this.onInitBaseSpeed()
    }

    onStopGame() {
        Laya.timer.clearAll(this);
        GameLogic.Share['_ctr_' + this.Display.name].speedFactor = 0;
    }

    onInitEff() {
        for (let i = 0; i < this.Eff.numChildren; i++) {
            this.Eff.getChildAt(i).active = false;
        }
    }

    _randomXMoveTime: number = 2;
    onStartGame() {
        this.onAddSpeedCB();
        // this._randomXMoveTime = Utility.GetRandom(1, 3);
        Laya.timer.loop(this._randomXMoveTime * 1000, this, this.onRandomXMove);
        // Laya.timer.loop(3000, this, this.onAddSpeedCB);
    }

    _roateNum: number = 0;
    onRandomXMove() {
        let leftOrRight = Utility.GetRandom(0, 1);
        let _x = Utility.GetRandom(0, 8);
        if (leftOrRight == 0) {
            _x *= -0.1;
        }else{
            _x *= 0.1;
        }
        this._roateNum = _x;
    }

    onRoateNodeMove() {
        let out = new Laya.Vector3();
        Laya.Vector3.lerp(this.RoateNode.transform.localPosition, new Laya.Vector3(this._roateNum, this.RoateNode.transform.localPosition.y, this.RoateNode.transform.localPosition.z), 0.01, out);
        this.RoateNode.transform.localPosition = out;
    }

    isGameOver: boolean = false;
    onCleanAllSpeed(all: any[]) {
        let ow = all[0] as Laya.Sprite3D;
        // if ()
        if (all != null && ow != null && ow.name != this.Display.name) return;
        this.isGameOver = true;
        this.Eff.getChildAt(0).active = false;
        Laya.timer.clear(this, this.AddSpeedLoop);
        Laya.timer.clear(this, this.DelSpeedLoop);
        Laya.timer.clear(this, this.AddBaseSpeedLoop);
        Laya.timer.clear(this, this.DelBaseSpeedLoop);
        GameLogic.Share['_ctr_' + this.Display.name].speedFactor = 0;
    }

    _poolId: number = 0;
    onChangeSkin() {
        for (let i = 0; i < this.Roles.numChildren; i++) {
            let role = this.Roles.getChildAt(i) as Laya.Sprite3D;
            role.active = false;
        }
        let roleId = Utility.GetRandom(0, 9);
        let role_use = this.Roles.getChildAt(roleId) as Laya.Sprite3D;
        role_use.active = true;
        this.Role = role_use.getComponent(Laya.Animator);


        for (let i = 0; i < this.Skate.numChildren; i++) {
            let role = this.Skate.getChildAt(i) as Laya.Sprite3D;
            role.active = false;
        }
        let skateId = Utility.GetRandom(0, 15);
        this.Skate.getChildAt(skateId).active = true;
        this._poolId = skateId;
        console.log(this.Display.name, skateId);
        switch (skateId) {
            case 0:
                this.ChangeAnimator(AnimName.idle_1);
                break;
            case 1:
                this.ChangeAnimator(AnimName.idle_3);
                break;
            case 2:
                this.ChangeAnimator(AnimName.idle_4);
                break;
            case 3:
                this.ChangeAnimator(AnimName.idle_6);
                break;
            case 4:
                this.ChangeAnimator(AnimName.idle_5);
                break;
            case 5:
                this.ChangeAnimator(AnimName.idle_1);
                break;
            case 6:
                this.ChangeAnimator(AnimName.idle_1);
                break;
            case 7:
                this.ChangeAnimator(AnimName.idle_2);
                break;
            case 8:
                this.ChangeAnimator(AnimName.idle_2);
                break;
            case 9:
                this.ChangeAnimator(AnimName.idle_2);
                break;
            case 10:
                this.ChangeAnimator(AnimName.idle_3);
                break;
            case 11:
                this.ChangeAnimator(AnimName.idle_5);
                break;
            case 12:
                this.ChangeAnimator(AnimName.idle_5);
                break;
            case 13:
                this.ChangeAnimator(AnimName.idle_6);
                break;
            case 14:
                this.ChangeAnimator(AnimName.idle_6);
                break;
            case 15:
                this.ChangeAnimator(AnimName.idle_4);
                break;
        }
        this.onInitBaseSpeed();
    }

    AnimName: AnimName = AnimName.idle_1;
    ChangeAnimator(animName_1: AnimName) {
        // if (this.AnimName == animName_1 && this.Role == _role) {
        //     return;
        // }
        this.AnimName = animName_1;
        console.log(this.Role, this.AnimName, animName_1)
        this.Role.play(animName_1);
        console.log(this.Role.getControllerLayer().name)
    }

    onShowFX_SpiltWater_1(isShow = true) {
        if (this.Eff.getChildByName("FX_SpiltWater_1").active == isShow) return;
        this.Eff.getChildByName("FX_SpiltWater_1").active = isShow;
    }

    isInWater: boolean = false;
    onBall_1CB() {
        if (this.isInvincible) {
            this.onOutInvincibleTime();
            return;
        }
        Laya.timer.clear(this, this.AddSpeedLoop);
        GameLogic.Share['_ctr_' + this.Display.name].speedFactor -= 0.3;
        Laya.timer.loop(10, this, this.AddSpeedLoop);
        this.onInInvincibleTime();
    }
    onTower_1CB() {
        if (this.isInvincible) {
            this.onOutInvincibleTime();
            return;
        }
        GameLogic.Share['_ctr_' + this.Display.name].speedFactor -= 0.3;
        Laya.timer.loop(10, this, this.AddSpeedLoop);
        this.onInInvincibleTime();
    }
    onBNowlingBottle_AllCB() {
        if (this.isInvincible) {
            this.onOutInvincibleTime();
            return;
        }
        GameLogic.Share['_ctr_' + this.Display.name].speedFactor -= 0.3;
        if (this.isAddSpeedBtnClick) {
            Laya.timer.loop(10, this, this.AddSpeedLoop);
        }
        this.onInInvincibleTime();
    }
    onMES_RailStopCB() {
        if (this.isInvincible) {
            this.onOutInvincibleTime();
            return;
        }
        Laya.timer.clear(this, this.AddSpeedLoop);
        GameLogic.Share['_ctr_' + this.Display.name].speedFactor -= 0.3;
        Laya.timer.loop(10, this, this.AddSpeedLoop);
        this.onInInvincibleTime();
    }

    isInvincible: boolean = false;
    onInInvincibleTime(isBuy: boolean = false) {
        this.isInvincible = true;
        // this.Eff.getChildAt(6).active = true;
        if (isBuy) return;
        Laya.timer.once(2000, this, this.onOutInvincibleTime)
    }

    onOutInvincibleTime() {
        this.isInvincible = false;
        // this.Eff.getChildAt(6).active = false;
    }

    baseAddNum: number = 0;
    baseAddNumMax: number = 0.5;
    onAddBaseSpeedCB() {
        this.Eff.getChildByName("FX_GetGold_4_1").active = true;
        this.baseAddNum = this.baseAddNumMax;
        if (this.isAddSpeedBtnClick) {
            Laya.timer.clear(this, this.AddSpeedLoop);
            Laya.timer.loop(10, this, this.AddSpeedLoop);
        } else { }
        return;
        Laya.timer.clear(this, this.AddBaseSpeedLoop);
        Laya.timer.clear(this, this.DelBaseSpeedLoop);
        Laya.timer.loop(10, this, this.AddBaseSpeedLoop);
    }
    AddBaseSpeedLoop() {
        if (this.baseAddNum >= this.baseAddNumMax) {
            this.baseAddNum = this.baseAddNumMax;
            // Laya.timer.clear(this, this.DelSpeedLoop);
            return;
        }
        // console.log(GameLogic.Share._ctr.speedFactor);
        this.baseAddNum += 0.02;
    }

    onDelBaseSpeedCB() {
        this.Eff.getChildByName("FX_GetGold_4_1").active = false;
        this.baseAddNum = 0;
        if (this.isAddSpeedBtnClick) {
            Laya.timer.clear(this, this.AddSpeedLoop);
            Laya.timer.loop(10, this, this.AddSpeedLoop);
        } else { }
        return
        Laya.timer.clear(this, this.AddBaseSpeedLoop);
        Laya.timer.clear(this, this.DelBaseSpeedLoop);
        Laya.timer.loop(10, this, this.DelBaseSpeedLoop);
    }
    DelBaseSpeedLoop() {
        if (this.baseAddNum <= 0) {
            this.baseAddNum = 0;
            Laya.timer.clear(this, this.DelSpeedLoop);
            return;
        }
        // console.log(GameLogic.Share._ctr.speedFactor);
        this.baseAddNum -= 0.005;
    }

    isStop: boolean = false;
    GameOver(isWin: boolean) {
        console.log("游戏结束");
        Laya.timer.clearAll(this);
        this.isStop = true;
        GameManager.Instance.AllPlayerRank.push(this.Display);
        GameLogic.Share['_ctr_' + this.Display.name].speedFactor = 0;
    }

    baseSpeed: number = 0.2;
    maxAddSpeed: number = 0.3;
    isAddSpeedBtnClick: boolean = false;

    onInitBaseSpeed() {
        if (GameManager.Instance._level_data.length <= 0) return;
        let lv: number = Math.floor((PlayerDataMgr.getPlayerData().grade - 1) % 5);
        let speed = GameManager.Instance._level_data[lv].Top_Ai[this._index - 1];
        let topSpeed = GameManager.Instance._level_data[lv].Rocket_Ai[this._index - 1];
        this.maxAddSpeed = this.baseSpeed + speed * 0.1 * 0.2 * 0.2;
        this.baseAddNumMax = this.maxAddSpeed + topSpeed * 0.1 * 0.2 * 0.2;
        // console.log("AIDate", this.Display.name, this.maxAddSpeed, this.baseAddNumMax);
    }
    // endSpeed: number = 0.2;
    onDelSpeedCB() {
        this.isAddSpeedBtnClick = false;
        Laya.timer.clear(this, this.AddSpeedLoop);
        Laya.timer.clear(this, this.DelSpeedLoop);
        Laya.timer.loop(10, this, this.DelSpeedLoop);
    }
    DelSpeedLoop() {
        let max = this.baseSpeed + this.baseAddNum;

        // console.log("??????????????????", GameLogic.Share['_ctr_' + this.Display.name].speedFactor, GameLogic.Share._ctr.speedFactor, max);
        if (GameLogic.Share._ctr.speedFactor <= max) {
            GameLogic.Share['_ctr_' + this.Display.name].speedFactor = max;
            if (this.isInWater) return;
            this.AddSpeedLoop();
            return;
        }
        GameLogic.Share['_ctr_' + this.Display.name].speedFactor -= 0.005;
    }

    onAddSpeedCB() {
        Laya.timer.clear(this, this.AddSpeedLoop);
        Laya.timer.clear(this, this.DelSpeedLoop);
        Laya.timer.loop(10, this, this.AddSpeedLoop);
        this.onShowFX_SpiltWater_1();
        this.isAddSpeedBtnClick = true;
    }
    AddSpeedLoop() {
        if (this.isGameOver) return;
        if (this.isInWater) return;
        let max = this.baseSpeed + this.maxAddSpeed + this.baseAddNum
        // console.log("AddMax---------->   ", GameLogic.Share._ctr.speedFactor, max, this.baseSpeed, this.baseAddNum)

        let addSpeed = 0.01 * ((5 + GameManager.Instance._level_data[Math.floor((PlayerDataMgr.getPlayerData().grade - 1) % 5)].Acc_Ai[this._index - 1]) / 15);
        // if (GameLogic.Share._ctr.speedFactor > max) {
        //     GameLogic.Share['_ctr_' + this.Display.name].speedFactor -= addSpeed;
        //     return;
        // }
        if (GameLogic.Share._ctr.speedFactor >= max) {
            GameLogic.Share['_ctr_' + this.Display.name].speedFactor = max;
            return;
        }
        // console.log(">>>>>>>>>>>>AI", GameLogic.Share['_ctr_' + this.Display.name].speedFactor, max);
        GameLogic.Share['_ctr_' + this.Display.name].speedFactor += addSpeed;
    }

    //移动相关
    LastPoint: Laya.Vector3;
    isClick: boolean = false;
    AimPoint: Laya.Vector3;
    //#region ==============  鼠标点击  ====================
    onKeyDown(e: Laya.Event): void {
        if (e.keyCode == Laya.Keyboard.A) {
            // let wagon_man_2 = (BuildPreFabe.GameScene.getChildByName("Man") as Laya.Sprite3D).clone() as Laya.Sprite3D;
            // this.AddMan(wagon_man_2);
        }
        if (e.keyCode == Laya.Keyboard.R) {
            // GameLogic.Share['_ctr_' + this.Display.name].speedFactor = 0;
        }
        if (e.keyCode == Laya.Keyboard.W) {
            // console.log(GameLogic.Share.isLeftOrRight);
            // GameLogic.Share['_ctr_' + this.Display.name].speedFactor = 0.5;
        }
        if (e.keyCode == Laya.Keyboard.Q) {
            // this.isLeftOrRight = "Left";
            // console.log(this.isLeftOrRight);
        }
        if (e.keyCode == Laya.Keyboard.E) {
            // this.isLeftOrRight = "Right";
            // console.log(this.isLeftOrRight);
        }
        if (e.keyCode == Laya.Keyboard.S) {
            Laya.timer.clear(this, this.AddSpeedLoop);
            Laya.timer.clear(this, this.DelSpeedLoop);
            Laya.timer.clearAll(this);
            GameLogic.Share['_ctr_' + this.Display.name].speedFactor = 0;
            // Laya.timer.loop(1, this, () => {
            //     // console.log(GameLogic.Share._ctr.speedFactor)
            //     GameLogic.Share['_ctr_' + this.Display.name].speedFactor = 0;
            // });
        }
        if (e.keyCode == Laya.Keyboard.M) {
            // GameLogic.Share['_ctr_' + this.Display.name].speedFactor = 0.5;
            // GameLogic.Share.PushMovePath(GameLogic.Share._movePath, GameLogic.Share._ctr, GameLogic.Share._player);
        }
    }
    //#endregion

    onUpdate(): void {
        // console.log(GameLogic.Share.speedFactor);
        // if (!GameLogic.Share.isStartGame) return;
        // this.onMove();
        // this.onRotation();
        this.onRoateNodeMove();
    }
}