import GameLogic from "../Crl/GameLogic"
import { GameManager } from "../Crl/GameManager"
import PlayerDataMgr from "../Libs/PlayerDataMgr"
import EventMgr, { EventType } from "../Mod/EventManager"
import Utility from "../Mod/Utility"
import SGAD from "../SGSDK/SGAD"
import SGMgr from "../SGSDK/SGMgr"

export default class GetDiamondUI extends Laya.Scene {
    constructor() {
        super()
    }

    BoxBg: Laya.Image;
    DescribeIcon: Laya.Image;
    Icon: Laya.Image;
    DiamondIcon: Laya.Image;
    CloseBtn: Laya.Image;
    VideoBtn: Laya.Image;

    onOpened() {
        this.size(Laya.stage.displayWidth, Laya.stage.displayHeight);

        // if (random == 0) {
        this.onInitUI();

        SGAD.showBannerAd();
        // SGAD.hideBannerAd();
    }


    onInitUI() {
        this.BoxBg.scale(0, 0);
        this.DescribeIcon.scale(0, 0);
        this.Icon.scale(0, 0);
        this.CloseBtn.scale(0, 0);
        this.VideoBtn.scale(0, 0);
        Laya.Tween.to(this.BoxBg, { scaleX: 1, scaleY: 1 }, 300, Laya.Ease.backOut, Laya.Handler.create(this, () => {

            Laya.Tween.to(this.DescribeIcon, { scaleX: 1, scaleY: 1 }, 300, Laya.Ease.backOut, Laya.Handler.create(this, () => {

                Laya.Tween.to(this.Icon, { scaleX: 1, scaleY: 1 }, 300, Laya.Ease.backOut, Laya.Handler.create(this, () => {

                    Laya.Tween.to(this.CloseBtn, { scaleX: 1, scaleY: 1 }, 300, Laya.Ease.backOut, Laya.Handler.create(this, () => {

                        Utility.addClickEvent(this.CloseBtn, this, this.closeCB);
                    }))
                    Laya.Tween.to(this.VideoBtn, { scaleX: 1, scaleY: 1 }, 300, Laya.Ease.backOut, Laya.Handler.create(this, () => {

                        Utility.addClickEvent(this.VideoBtn, this, this.onVideoBtnCB);
                    }))
                }))
            }))
        }))
    }

    onClosed() {
        SGAD.hideBannerAd();
    }

    onVideoBtnCB() {
        SGAD.showVideoAd(() => {
            GameLogic.Share.SpawnUIAddDiamond(this.VideoBtn, this.DiamondIcon, this.BoxBg.parent, this, false);
            Laya.timer.once(1000, this, () => {
                PlayerDataMgr.getPlayerData().diamond += 25;
                PlayerDataMgr.setPlayerData();
                EventMgr.SendEvent(EventType.GetCoinAndDiamondUpdate);
                this.closeCB();
            })
        })
    }


    closeCB() {
        Laya.Tween.to(this.BoxBg, { scaleX: 0, scaleY: 0 }, 300, Laya.Ease.backIn, Laya.Handler.create(this, () => {

            Laya.Scene.close('MyScenes/GetDiamondUI.scene');
        }))
        // GameLogic.Share.restartGame();
        // Laya.Scene.open('MyScenes/GameUI.scene');
    }
}
