import SGAD from "../SGAD"
import SGMgr from "../SGMgr"

export default class SGCustom_OPPO extends Laya.Scene {
    constructor() {
        super()
    }

    ccb: Function = null
    btnClose: Laya.Image

    onClosed(type?: string): void {
        SGAD.hideCustomAd()
        this.ccb && this.ccb()
    }

    onOpened(param: any): void {
        this.size(Laya.stage.displayWidth, Laya.stage.displayHeight)
        this.ccb = param != undefined ? param.ccb : null
        SGMgr.addClickEvent(this.btnClose, this, this.btnCloseCB)
        this.btnClose.visible = false
        SGAD.showCustomAd(() => {
            this.btnClose.visible = true
        }, () => {
            Laya.timer.frameOnce(1, this, this.btnCloseCB)
        })
    }

    btnCloseCB() {
        this.close()
    }
}