import AIController from "../Crl/role/AIController"
import GameLogic from "../Crl/GameLogic"
import { GameManager } from "../Crl/GameManager"
import PlayerController from "../Crl/role/PlayerController"
import PlayerDataMgr from "../Libs/PlayerDataMgr"
import EventMgr, { EventType } from "../Mod/EventManager"
import SoundMgr from "../Mod/SoundMgr"
import Utility from "../Mod/Utility"
import SGAD from "../SGSDK/SGAD"
import SGMgr from "../SGSDK/SGMgr"

export default class FinishUI extends Laya.Scene {
    constructor() {
        super()
    }

    SettlementBox: Laya.Image;
    AllStartBox: Laya.Box;
    start_1: Laya.Image;
    start_2: Laya.Image;
    start_3: Laya.Image;
    EndScroeBox: Laya.Image;
    expGetDiamodIcon: Laya.Image;
    StuntLabel: Laya.Label;
    RankLabel: Laya.Label;
    CollectionLabel: Laya.Label;

    GetCoinLabel: Laya.Label;

    EndLevelExpProBox: Laya.Box;
    ExpPrp: Laya.Label;
    NextLevelLabel: Laya.Label;
    NowLevelLabel: Laya.Label;
    AgainGameBtn: Laya.Image;
    NextGameBtn: Laya.Image;

    EndGetCoinBox: Laya.Image;
    EndGetCoinBoxBG: Laya.Box;
    EndGetCoinBoxTitle: Laya.Image;
    EndGetCoinBoxIcon: Laya.Image;
    CloseEndGetCoinBoxBtn: Laya.Image;
    GetEndCoinBtn: Laya.Image;

    FailBox: Laya.Image;
    FailTitle: Laya.Image;
    FailIcon: Laya.Image;
    FailTip: Laya.Image;
    FailGoHomeBtn: Laya.Image;
    ReviveBtn: Laya.Image;

    CoinIcon: Laya.Image;
    DiamondIcon: Laya.Image;

    onOpened() {
        this.size(Laya.stage.displayWidth, Laya.stage.displayHeight);


        // PlayerDataMgr.getPlayerData().freeskinId = -1;
        // PlayerDataMgr.getPlayerData().ManFreeSkinId = -1;

        SGMgr.inFinish();

        this.onInitUI();
    }

    onReviveBtnCB() {
        Utility.showVideoAd(() => {
            SoundMgr.instance.playSoundEffect("UIShowDown");
            Laya.Tween.to(this.FailBox, { scaleX: 0, scaleY: 0 }, 300, Laya.Ease.backIn, Laya.Handler.create(this, () => {
                // this.onInitEndGetCoinBox();
                //TODO结算页重新开始游戏
                GameLogic.Share.ReviveGame();
            }));
        }, () => { Utility.showToast("看完视频才能获得奖励噢!") })
    }

    onFailGoHomeBtnCB() {
        SoundMgr.instance.playSoundEffect("UIShowDown");
        Laya.Tween.to(this.FailBox, { scaleX: 0, scaleY: 0 }, 300, Laya.Ease.backIn, Laya.Handler.create(this, () => {
            SGMgr.backToHome(() => {
                GameLogic.Share.restartGame();
                GameLogic.Share.gameGoHome();
            })
        }));
    }

    onClosed() {
    }

    onInitUI() {
        // this.onInitRankingBox();
        if (GameLogic.Share.isWin) {
            this.onInitSettlementBox();
        } else {
            this.InitFailBox();
        }
    }
    InitFailBox() {

        this.FailTitle.scaleX = 0;
        this.FailIcon.scale(0, 0);
        this.FailTip.scale(0, 0);
        this.FailGoHomeBtn.centerX = -610;
        this.ReviveBtn.centerX = 610;
        this.FailBox.visible = true;
        SoundMgr.instance.playSoundEffect("UIShow");
        Laya.Tween.to(this.FailTitle, { scaleX: 1, scaleY: 1 }, 300, Laya.Ease.bounceOut, Laya.Handler.create(this, () => {

            SoundMgr.instance.playSoundEffect("UIShow");
            Laya.Tween.to(this.FailTip, { scaleX: 1, scaleY: 1 }, 300, Laya.Ease.backOut);
            Laya.Tween.to(this.FailIcon, { scaleX: 1, scaleY: 1 }, 300, Laya.Ease.bounceOut, Laya.Handler.create(this, () => {
                // this.WinCoinBoxAnim(this.loseCoinBox, 1000);

                SoundMgr.instance.playSoundEffect("UIShow");
                Laya.Tween.to(this.ReviveBtn, { centerX: 157 }, 300, Laya.Ease.backOut, Laya.Handler.create(this, () => {
                    Utility.addClickEvent(this.ReviveBtn, this, this.onReviveBtnCB);
                }));
                Laya.Tween.to(this.FailGoHomeBtn, { centerX: -157 }, 300, Laya.Ease.backOut, Laya.Handler.create(this, () => {
                    Utility.addClickEvent(this.FailGoHomeBtn, this, this.onFailGoHomeBtnCB);
                }));
            }));
        }));
    }

    isOpenLuckDrawUI: boolean = false;
    onInitSettlementBox() {
        this.start_1.scale(0, 0);
        this.start_2.scale(0, 0);
        this.start_3.scale(0, 0);
        this.EndScroeBox.scale(0, 0);
        this.EndLevelExpProBox.scale(0, 0);
        this.AgainGameBtn.scale(0, 0);
        this.NextGameBtn.scale(0, 0);
        for (let i = 0; i < this.AllStartBox.numChildren; i++) {
            let star = this.AllStartBox.getChildAt(i) as Laya.Image;
            (star.getChildAt(0) as Laya.Image).visible = false;
            (star.getChildAt(2) as Laya.Image).visible = false;
        }
        for (let i = 0; i < 4 - GameManager.Instance.PlayerRangNum; i++) {
            let star = this.AllStartBox.getChildAt(i) as Laya.Image;
            (star.getChildAt(0) as Laya.Image).visible = true;
            (star.getChildAt(2) as Laya.Image).visible = true;
        }
        this.RankLabel.text = GameManager.Instance._level_data[0].Gold_Get.toString();
        this.CollectionLabel.text = GameManager.Instance.PlayerGetCoinNum.toString();
        this.GetCoinLabel.text = (GameManager.Instance._level_data[0].Gold_Get + GameManager.Instance.PlayerGetCoinNum).toString();
        this.NowLevelLabel.text = PlayerDataMgr.getPlayerData().playerLv.toString();
        this.NextLevelLabel.text = (PlayerDataMgr.getPlayerData().playerLv + 1).toString();
        //TODO经验条
        let pro = (1 - PlayerDataMgr.getPlayerData().exp / GameManager.Instance._exp_data[PlayerDataMgr.getPlayerData().playerLv - 1].exp) * 576;
        this.ExpPrp.x = -pro;


        this.SettlementBox.visible = true;
        SoundMgr.instance.playSoundEffect("UIShow");
        Laya.Tween.to(this.start_1, { scaleX: 1, scaleY: 1 }, 200, Laya.Ease.backOut, Laya.Handler.create(this, () => {

            SoundMgr.instance.playSoundEffect("UIShow");
            Laya.Tween.to(this.start_2, { scaleX: 1, scaleY: 1 }, 200, Laya.Ease.backOut, Laya.Handler.create(this, () => {

                SoundMgr.instance.playSoundEffect("UIShow");
                Laya.Tween.to(this.start_3, { scaleX: 1, scaleY: 1 }, 200, Laya.Ease.backOut, Laya.Handler.create(this, () => {

                }))
            }))
        }))
        SoundMgr.instance.playSoundEffect("UIShow");
        Laya.Tween.to(this.EndScroeBox, { scaleX: 1, scaleY: 1 }, 500, Laya.Ease.backOut, Laya.Handler.create(this, () => {

            SoundMgr.instance.playSoundEffect("UIShow");
            Laya.Tween.to(this.EndLevelExpProBox, { scaleX: 1, scaleY: 1 }, 500, Laya.Ease.backOut, Laya.Handler.create(this, () => {


                let getExpNum = GameManager.Instance._level_data[0].Exp_Get[GameManager.Instance.PlayerRangNum - 1];
                PlayerDataMgr.getPlayerData().exp += getExpNum;
                if (PlayerDataMgr.getPlayerData().exp >= GameManager.Instance._exp_data[PlayerDataMgr.getPlayerData().playerLv - 1].exp) {
                    PlayerDataMgr.getPlayerData().exp = PlayerDataMgr.getPlayerData().exp - GameManager.Instance._exp_data[PlayerDataMgr.getPlayerData().playerLv - 1].exp;
                    PlayerDataMgr.getPlayerData().playerLv++;
                    PlayerDataMgr.getPlayerData().isGetFinishExpProp_1 = false;
                    PlayerDataMgr.getPlayerData().isGetFinishExpProp_2 = false;
                    PlayerDataMgr.getPlayerData().isGetFinishExpProp_3 = false;
                    // this.isOpenLuckDrawUI = true;
                    this.NowLevelLabel.text = PlayerDataMgr.getPlayerData().playerLv.toString();
                    this.NextLevelLabel.text = (PlayerDataMgr.getPlayerData().playerLv + 1).toString();
                    this.ExpPrp.x = -576;
                }
                PlayerDataMgr.setPlayerData();
                let pro_end = (1 - PlayerDataMgr.getPlayerData().exp / GameManager.Instance._exp_data[PlayerDataMgr.getPlayerData().playerLv - 1].exp) * 576;
                Laya.Tween.clearAll(this.ExpPrp);
                Laya.Tween.to(this.ExpPrp, { x: -pro_end }, 200, Laya.Ease.linearIn, Laya.Handler.create(this, () => {
                    if (pro_end <= 433 && !PlayerDataMgr.getPlayerData().isGetFinishExpProp_1) {
                        PlayerDataMgr.getPlayerData().isGetFinishExpProp_1 = true;
                        PlayerDataMgr.getPlayerData().diamond += 10;
                        PlayerDataMgr.setPlayerData();
                        GameLogic.Share.SpawnUIAddDiamond(this.expGetDiamodIcon, this.DiamondIcon, this.SettlementBox, this, false);
                    }
                    else if (pro_end <= 287 && !PlayerDataMgr.getPlayerData().isGetFinishExpProp_2) {
                        PlayerDataMgr.getPlayerData().isGetFinishExpProp_2 = true;
                        PlayerDataMgr.setPlayerData();
                        GameLogic.Share.showLuckDrawUI();
                    }
                    else if (pro_end <= 140 && !PlayerDataMgr.getPlayerData().isGetFinishExpProp_3) {
                        PlayerDataMgr.getPlayerData().isGetFinishExpProp_3 = true;
                        PlayerDataMgr.setPlayerData();
                        GameLogic.Share.showLuckDrawUI();
                    }
                }));

                SoundMgr.instance.playSoundEffect("UIShow");
                Laya.Tween.to(this.AgainGameBtn, { scaleX: 1, scaleY: 1 }, 500, Laya.Ease.backOut, Laya.Handler.create(this, () => {
                    Utility.addClickEvent(this.AgainGameBtn, this, this.onAgainGameBtnCB);
                }))
                Laya.Tween.to(this.NextGameBtn, { scaleX: 1, scaleY: 1 }, 500, Laya.Ease.backOut, Laya.Handler.create(this, () => {
                    Utility.addClickEvent(this.NextGameBtn, this, this.onNextGameBtnCB);
                }))
            }))
        }))
    }

    onAgainGameBtnCB() {
        SoundMgr.instance.playSoundEffect("UIShowDown");
        Laya.Tween.to(this.SettlementBox, { scaleX: 0, scaleY: 0 }, 300, Laya.Ease.backIn, Laya.Handler.create(this, () => {
            // this.onInitEndGetCoinBox();
            //TODO结算页重新开始游戏
            GameLogic.Share.restartGame(true);
            Laya.Scene.open('MyScenes/GameUI.scene');
        }));
    }

    onNextGameBtnCB() {
        SoundMgr.instance.playSoundEffect("UIShow");
        PlayerDataMgr.getPlayerData().coin += (GameManager.Instance._level_data[0].Gold_Get + GameManager.Instance.PlayerGetCoinNum);
        PlayerDataMgr.setPlayerData();
        GameLogic.Share.SpawnUIAddCoin(this.NextGameBtn, this.CoinIcon, this.SettlementBox, this, false);
        Laya.timer.once(1000, this, () => {
            Laya.Tween.to(this.SettlementBox, { scaleX: 0, scaleY: 0 }, 300, Laya.Ease.backIn, Laya.Handler.create(this, () => {
                this.onInitEndGetCoinBox();
            }));
        })
    }

    onInitEndGetCoinBox() {
        this.SettlementBox.visible = false;
        this.EndGetCoinBoxBG.scale(0, 0);
        this.EndGetCoinBoxTitle.scale(0, 0);
        this.EndGetCoinBoxIcon.scale(0, 0);
        this.CloseEndGetCoinBoxBtn.scale(0, 0);
        this.GetEndCoinBtn.scale(0, 0);
        this.EndGetCoinBox.visible = true;
        SoundMgr.instance.playSoundEffect("UIShow");
        Laya.Tween.to(this.EndGetCoinBoxBG, { scaleX: 1, scaleY: 1 }, 300, Laya.Ease.backOut, Laya.Handler.create(this, () => {

            SoundMgr.instance.playSoundEffect("UIShow");
            Laya.Tween.to(this.EndGetCoinBoxTitle, { scaleX: 1, scaleY: 1 }, 300, Laya.Ease.backOut, Laya.Handler.create(this, () => {

                SoundMgr.instance.playSoundEffect("UIShow");
                Laya.Tween.to(this.EndGetCoinBoxIcon, { scaleX: 1, scaleY: 1 }, 300, Laya.Ease.backOut, Laya.Handler.create(this, () => {

                    SoundMgr.instance.playSoundEffect("UIShow");
                    Laya.Tween.to(this.CloseEndGetCoinBoxBtn, { scaleX: 1, scaleY: 1 }, 500, Laya.Ease.backOut, Laya.Handler.create(this, () => {
                        Utility.addClickEvent(this.CloseEndGetCoinBoxBtn, this, this.onCloseEndGetCoinBoxBtnCB);
                    }))
                    Laya.Tween.to(this.GetEndCoinBtn, { scaleX: 1, scaleY: 1 }, 500, Laya.Ease.backOut, Laya.Handler.create(this, () => {
                        Utility.addClickEvent(this.GetEndCoinBtn, this, this.onGetEndCoinBtn);
                    }))
                }))
            }))
        }))
    }

    onCloseEndGetCoinBoxBtnCB() {
        SoundMgr.instance.playSoundEffect("UIShow");
        Laya.Tween.to(this.EndGetCoinBoxBG, { scaleX: 0, scaleY: 0 }, 300, Laya.Ease.backIn, Laya.Handler.create(this, () => {
            SGMgr.backToHome(() => {
                if (GameLogic.Share.isWin) {
                    PlayerDataMgr.getPlayerData().grade++;
                    PlayerDataMgr.setPlayerData();
                }
                GameLogic.Share.restartGame();
                GameLogic.Share.gameGoHome();
            })
        }));
    }

    onGetEndCoinBtn() {
        Utility.showVideoAd(() => {
            GameLogic.Share.SpawnUIAddCoin(this.GetEndCoinBtn, this.CoinIcon, this.EndGetCoinBox.parent, this, false);
            Laya.timer.once(1000, this, () => {
                PlayerDataMgr.getPlayerData().coin += 2000;
                PlayerDataMgr.setPlayerData();
                this.onCloseEndGetCoinBoxBtnCB();
            })
        }, () => { Utility.showToast("看完视频才能获得奖励噢!") })
    }

}
