import GameLogic from "../../Crl/GameLogic";
import PlayerDataMgr from "../../Libs/PlayerDataMgr"
import EventMgr, { EventType } from "../EventManager";
import Utility from "../Utility";

export default class DiamondNodeUI extends Laya.Script {
    constructor() {
        super()
    }

    Display: Laya.Sprite = null

    getDiamondBtn: Laya.Image;
    diamondLabel: Laya.FontClip;

    onAwake() {
        this.Display = this.owner as Laya.Sprite;
        this.getDiamondBtn = this.Display.getChildAt(1) as Laya.Image;
        this.diamondLabel = this.Display.getChildAt(2) as Laya.FontClip;

        Utility.addClickEvent(this.getDiamondBtn, this, this.onGetCoinBtnCB);
        EventMgr.AddListener(EventType.UpdateAllCoinNodeUI, this, this.onUpdateUI);
    }

    onEnable(): void {
        this.onUpdateUI();
    }

    onDisable(): void {
        EventMgr.Off(EventType.UpdateAllCoinNodeUI, this, this.onUpdateUI);
    }

    onGetCoinBtnCB() {
        GameLogic.Share.showGetDiamondUI();
    }

    onUpdateUI() {
        // this.CoinLabel.text = Utility.formatCurrencyWithSuffix(PlayerDataMgr.getPlayerData().coin);
        this.diamondLabel.value = PlayerDataMgr.getPlayerData().diamond.toString();
    }

    onUpdate() {
        this.onUpdateUI();
    }
}