export class EventManager {

    private mEvent: Laya.EventDispatcher = null;

    private static inst: EventManager = null;

    // public static get Inst(): EventManager {
    //     if(EventManager.inst == null) {
    //         EventManager.inst = new EventManager();
    //     }
    //     return EventManager.inst
    // }

    constructor() {
        this.mEvent = new Laya.EventDispatcher();
    }

    public HasListener(event): boolean {
        return this.mEvent.hasListener(event);
    }

    public AddListener(event, caller: any, listener: Function, args?: any | null, clearbeforEvent?: boolean) {
        this.mEvent.on(event, caller, listener, args);
        if (clearbeforEvent) {
            this.mEvent.offAllCaller(caller);
        }
    }

    public AddOneListener(event, caller: any, listener: Function, args?: any | null, clearbeforEvent?: boolean) {
        this.mEvent.once(event, caller, listener, args);
        if (clearbeforEvent) {
            this.mEvent.offAllCaller(caller);
        }
    }

    public SendEvent(event, data: any = {}) {
        if (!this.HasListener(event)) {
            console.log("事件不存在", event);
            return;
        }
        this.mEvent.event(event, [data]);
    }

    /**
     * 从 EventDispatcher 对象中删除侦听器。
     * @param type 事件的类型。
     * @param caller 事件侦听函数的执行域。
     * @param listener 事件侦听函数。
     * @param onceOnly （可选）如果值为 true ,则只移除通过 once 方法添加的侦听器。
     * @return 此 EventDispatcher 对象。
     */
    public Off(event, caller: any, listener: Function, onceOnly: boolean = false) {
        this.mEvent.off(event, caller, listener, onceOnly);
    }

    /**
     * 清除该事件上所有事件
     * @param event 事件
     */
    public OffAll(event) {
        this.mEvent.offAll(event);
    }

    public OffAllCaller(caller: any) {
        this.mEvent.offAllCaller(caller);
    }

}

