    public static getCostById(id) {
        let cost: number = 0
        switch (id) {
            case 0:
                cost = 0
                break
            case 1:
                cost = 400
                break
            case 2:
                cost = 800
                break
            case 3:
                cost = 1200
                break
            case 4:
                cost = 1600
                break
            case 5:
                cost = 2000
                break
        }
        return cost
    }
