export default class UpDownLoop extends Laya.Script {
    constructor() {
        super()
    }

    myOwner: Laya.Image
    startY: number = 0

    /** @prop {name:addY, tips:"向下浮动的Y值得参数", type:Int, default:50}*/
    public addY: number = 50;

    onAwake(): void {
        this.myOwner = this.owner as Laya.Image
        this.startY = this.myOwner.y
        this.startTween()
    }

    startTween() {
        Laya.Tween.to(this.myOwner, { y: this.startY + this.addY }, 1000, Laya.Ease.sineInOut, new Laya.Handler(this, () => {
            Laya.Tween.to(this.myOwner, { y: this.startY }, 1000, Laya.Ease.sineInOut, new Laya.Handler(this, () => {
                this.startTween()
            }))
        }))
    }
}