import PlayerDataMgr from "../../Libs/PlayerDataMgr";
import PhysicsCrushAnim from "../../Mod/3D/PhysicsCrushAnim";
import Utility from "../../Mod/Utility";
import GameLogic from "../GameLogic";
import PlayerController from "../role/PlayerController";

export default class TowerController extends Laya.Script3D {

    Display: Laya.Sprite3D;
    all: Laya.Sprite3D

    onAwake() {
        this.Display = this.owner as Laya.Sprite3D;
        this.all = Utility.FindChildByName("all", this.Display) as Laya.Sprite3D;
    }

    // onInitAnim() {
    //     Ball
    // }

    onBreakTopBox() {
        this.Display.getComponent(Laya.PhysicsCollider).enabled = false;
        for (let i = 0; i < this.all.numChildren; i++) {
            let randomX = Utility.GetRandom(0, 2, false);
            let randomY = Utility.GetRandom(0, 1, false);
            let randomZ = Utility.GetRandom(0, 2, false);
            let block = this.all.getChildAt(i) as Laya.Sprite3D;
            let ctr = block.addComponent(PhysicsCrushAnim) as PhysicsCrushAnim;
            ctr.isWord = true;
            if (Utility.GetRandom(1, 2) == 1) {
                randomX = -randomX;
                randomZ = -randomZ;
            }
            let BezierPoints = Utility.BezierPoints(
                block.transform.position,
                new Laya.Vector3(block.transform.position.x + (randomX / 2), block.transform.position.y + randomY, block.transform.position.z + (randomZ / 2)),
                new Laya.Vector3(block.transform.position.x + randomX, this.all.transform.position.y - 2, block.transform.position.z + randomZ),
                50
            );
            ctr.bemonve_1(block, BezierPoints, this, () => {
                block.active = false;
            });
        }
    }

    onUpdate(): void {
    }
}