export default class BoxController extends Laya.Script3D {

    Display: Laya.Sprite3D;

    onAwake() {
        this.Display = this.owner as Laya.Sprite3D;
    }

    onEnable(): void {
        // this.onInit();
    }

    onInit() {
        if (Laya.Browser.onWeiXin && SGConfig.data.front_is_show_water_box) {
            this.Display.active = true;
        } else {
            this.Display.active = false;
        }
    }

    onUpdate(): void {
    }
}