import EventMgr, { EventType } from "../Mod/EventManager";
import Utility from "../Mod/Utility";
import AIController from "./role/AIController";
import GameLogic from "./GameLogic";
import PlayerController from "./role/PlayerController";
import TraceCircuit from "./TraceCircuit";

export default class TraceProgressTracker extends Laya.Script3D {
    public circuit: TraceCircuit;

    private lookAheadForTargetOffset: number = 1;
    private lookAheadForTargetFactor: number = 0.1;
    private lookAheadForSpeedOffset: number = 1;
    private lookAheadForSpeedFactor: number = 0.01;

    private isSmoothAlongRoute: boolean = true;

    /**终点 */
    public endDistance: number;

    public self: Laya.Sprite3D;
    public target: Laya.Transform3D;

    /**生成点 */
    public progressDistance: number = 0;
    /**速度 */
    public speedFactor: number = 0.1; //0.2

    public progressNum: number = 0;
    private lastPosition: Laya.Vector3 = new Laya.Vector3();
    public speed: number = 0;

    public progressPoint: RoutePoint;

    /** 挂载目标 */
    public Ctr_Target: Laya.Sprite3D;

    onAwake() {
        this.self = this.owner as Laya.Sprite3D;
        this.target = this.self.transform;
    }

    initCtrTarget(target: Laya.Sprite3D) {
        this.Ctr_Target = target;
    }
    init(minDistance = 0, randomDistance = 0) {
        this.speed = 0;
        this.reset(0, Math.random() * randomDistance + minDistance);
    }

    oriY: number = 0;
    recalcPos() {
        // if (!this.role) {
        //     this.role = this.self.getChildAt(0) as Laya.Sprite3D;
        //     this.oriY = this.role.transform.localPosition.y;
        // }

        // let configs = GameLogic.Share.configPaths;
        // let isSet = false
        // for (let i = configs.length - 1; i >= 0; i--) {
        //     if (this.progressDistance > configs[i][2]) {
        //         isSet = true;
        //         this.setRoadConfig(configs[i][1]);
        //         break;
        //     }
        // }
        // if (!isSet) {
        //     this.setRoadConfig(0);
        // }

        // let pos = this.role.transform.localPosition.clone();
        // pos.y = this.getY(pos) + this.oriY;
        // // Laya.Vector3.lerp(this.role.transform.localPosition.clone(), pos, 0.3, pos);
        // this.role.transform.localPosition = pos;
        // this.ctrlCarLR(pos);
    }

    reset(num = 0, distance = 5) {
        if (!this.circuit) return;
        // console.log(distance);
        // if (!this.progressDistance) this.progressDistance = 100; //随机起始点（复活点）

        this.progressNum = num;
        this.progressDistance = distance;
        if (!this.isSmoothAlongRoute) {
            // console.log(111111111111);
            //Nigel New
            this.target.position = this.circuit.allPointVec3[this.progressNum].clone();//this.circuit.waypoints[this.progressNum].position;
            this.target.rotation = this.circuit.allRotVec3[this.progressNum].clone();//this.circuit.waypoints[this.progressNum].rotation;
        }
        else {
            this.target.position = this.circuit.getRoutePoint(this.progressDistance + this.lookAheadForTargetOffset * this.speedFactor + this.lookAheadForTargetFactor * this.speed).position;
            // console.log(22222222222, this.target);
            let rot = new Laya.Quaternion();
            let quaternion = this.circuit.getRoutePoint(this.progressDistance + this.lookAheadForSpeedOffset + this.lookAheadForSpeedFactor * this.speed).direction;
            let upDir = this.circuit.getUpDirection(this.progressDistance + this.lookAheadForSpeedOffset + this.lookAheadForSpeedFactor * this.speed);
            Laya.Quaternion.rotationLookAt(quaternion, upDir, rot);
            Laya.Quaternion.invert(rot, rot);
            this.target.rotation = rot;
            this.lastPosition = this.target.position.clone();
        }
    }

    finish() {
        // console.log("移动结束");
        this.speedFactor = 0;
        if (this.Ctr_Target != null)
            EventMgr.SendEvent(EventType.CleanAllSpeed, [this.Ctr_Target])
        // if (GameLogic.Share.isLeftOrRight == 'left') {
        //     GameLogic.Share.isLeftOrRight = 'right';
        // } else {
        //     GameLogic.Share.isLeftOrRight = 'left';
        // }
        // GameLogic.Share.PushMovePath(GameLogic.Share._movePath, GameLogic.Share._ctr, GameLogic.Share._player);
        if (this.Ctr_Target != null && this.Ctr_Target.name == "Player") {
            this.Ctr_Target.getComponent(PlayerController).GameOver(true);
        }
        else if (this.Ctr_Target != null && this.Ctr_Target.name != "Player") {
            this.Ctr_Target.getComponent(AIController).GameOver(true);
        }
    }

    onUpdate() {
        if (GameLogic.Share.isStopGame) return;
        if (this.circuit == null) return;
        if (this.speedFactor == 0) {
            return;
        }
        if (this.self.destroyed) return;

        // console.log(this.progressDistance, this.circuit.accumulateDistance - 2);
        if (this.progressDistance >= this.circuit.accumulateDistance - 2) {
            this.finish();
            return;
        }

        if (this.isSmoothAlongRoute) {
            let deltaTime = 0.02;
            if (deltaTime > 0) {
                let v3 = new Laya.Vector3();
                Laya.Vector3.subtract(this.lastPosition, this.self.transform.position.clone(), v3);
                let length = Laya.Vector3.scalarLength(v3);
                this.speed = Laya.MathUtil.lerp(this.speed, length / deltaTime, deltaTime); //===============不确定==============
            }
            this.target.position = this.circuit.getRoutePoint(this.progressDistance + this.lookAheadForTargetOffset * this.speedFactor + this.lookAheadForTargetFactor * this.speed).position;
            let rot = new Laya.Quaternion();

            //TODO 测试注释            
            let quaternion = this.circuit.getRoutePoint(this.progressDistance + this.lookAheadForSpeedOffset + this.lookAheadForSpeedFactor * this.speed).direction;
            let upDir = this.circuit.getUpDirection(this.progressDistance + this.lookAheadForSpeedOffset + this.lookAheadForSpeedFactor * this.speed);

            Laya.Quaternion.rotationLookAt(quaternion, upDir, rot);
            Laya.Quaternion.invert(rot, rot);
            Laya.Quaternion.lerp(this.target.rotation, rot, 0.1, rot); //0.2
            this.target.rotation = rot;

            this.progressPoint = this.circuit.getRoutePoint(this.progressDistance);
            var progressDelta = this.circuit.v3Subtract(this.progressPoint.position, this.self.transform.position);
            if (Laya.Vector3.dot(progressDelta, this.progressPoint.direction) > 0) {
                this.progressDistance += Laya.Vector3.scalarLength(progressDelta) * 0.5;
            }
            this.lastPosition = this.self.transform.position.clone();
        }
    }

    role: Laya.Sprite3D;
    curRoadIndex: number = -1;
    lookAtTarget: Laya.Sprite3D;
    lookAtTmpTarget: Laya.Sprite3D;

    maxPosX: number;
    getY(pos) {
        if (this.curRoadIndex == 1) {
            let xx = Math.pow(pos.x, 2);
            let aa = Math.pow(6, 2);
            let bb = Math.pow(3, 2)
            pos.y = -Math.sqrt(bb - (bb / aa * xx)) + 3;
        }
        else if (this.curRoadIndex == 2) {
            let rr = Math.pow(13, 2);
            let xx = Math.pow(pos.x, 2);

            pos.y = -Math.sqrt(rr - xx) + 13;
        }
        else {
            pos.y = 0//pos.y;
        }
        // this.role.transform.localPosition = pos;
        return pos.y;
    }

    getRot(pos, index) {
        if (index == 1) {
            if (!this.lookAtTarget) {
                let lookAt = new Laya.Sprite3D;
                this.self.addChild(lookAt);
                this.lookAtTarget = lookAt;

                let lookAtTmp = new Laya.Sprite3D;
                this.self.addChild(lookAtTmp);
                this.lookAtTmpTarget = lookAtTmp;
            }

            this.lookAtTarget.transform.localPosition = new Laya.Vector3(0, 6, 0);
            this.lookAtTmpTarget.transform.position = this.role.transform.position.clone();
            this.lookAtTmpTarget.transform.lookAt(this.lookAtTarget.transform.position.clone(), new Laya.Vector3(0, 0, 1));
            let rot = this.lookAtTmpTarget.transform.localRotationEuler.clone();
            rot.setValue(0, 0, pos.x > 0 ? 90 - rot.x : rot.x - 90);
            return rot;
        }
        else if (index == 2) {
            if (!this.lookAtTarget) {
                let lookAt = new Laya.Sprite3D;
                this.self.addChild(lookAt);
                this.lookAtTarget = lookAt;

                let lookAtTmp = new Laya.Sprite3D;
                this.self.addChild(lookAtTmp);
                this.lookAtTmpTarget = lookAtTmp;
            }

            this.lookAtTarget.transform.localPosition = new Laya.Vector3(0, 13, 0);
            this.lookAtTmpTarget.transform.position = this.role.transform.position.clone();
            this.lookAtTmpTarget.transform.lookAt(this.lookAtTarget.transform.position.clone(), new Laya.Vector3(0, 0, 1));
            let rot = this.lookAtTmpTarget.transform.localRotationEuler.clone();
            rot.setValue(0, 0, pos.x > 0 ? 90 - rot.x : rot.x - 90);
            return rot;
        }
        else {
            return new Laya.Vector3;
        }
    }

    ctrlCarLR(pos) {
        if (this.curRoadIndex == 1) {
            if (!this.lookAtTarget) {
                let lookAt = new Laya.Sprite3D;
                this.self.addChild(lookAt);
                this.lookAtTarget = lookAt;

                let lookAtTmp = new Laya.Sprite3D;
                this.self.addChild(lookAtTmp);
                this.lookAtTmpTarget = lookAtTmp;
            }

            this.lookAtTarget.transform.localPosition = new Laya.Vector3(0, 6, 0);
            this.lookAtTmpTarget.transform.position = this.role.transform.position.clone();
            this.lookAtTmpTarget.transform.lookAt(this.lookAtTarget.transform.position.clone(), new Laya.Vector3(0, 0, 1));
            let rot = this.lookAtTmpTarget.transform.localRotationEuler.clone();
            // rot.setValue(0, oldRot.y, pos.x > 0 ? 90 - rot.x : rot.x - 90);
            rot.setValue(0, 0, pos.x > 0 ? 90 - rot.x : rot.x - 90);
            Laya.Vector3.lerp(this.role.transform.localRotationEuler.clone(), rot, 0.5, rot);
            this.role.transform.localRotationEuler = rot;
        }
        else if (this.curRoadIndex == 2) {
            if (!this.lookAtTarget) {
                let lookAt = new Laya.Sprite3D;
                this.self.addChild(lookAt);
                this.lookAtTarget = lookAt;

                let lookAtTmp = new Laya.Sprite3D;
                this.self.addChild(lookAtTmp);
                this.lookAtTmpTarget = lookAtTmp;
            }

            this.lookAtTarget.transform.localPosition = new Laya.Vector3(0, 13, 0);
            this.lookAtTmpTarget.transform.position = this.role.transform.position.clone();
            this.lookAtTmpTarget.transform.lookAt(this.lookAtTarget.transform.position.clone(), new Laya.Vector3(0, 0, 1));
            let rot = this.lookAtTmpTarget.transform.localRotationEuler.clone();
            // rot.setValue(0, oldRot.y, pos.x > 0 ? 90 - rot.x : rot.x - 90);
            rot.setValue(0, 0, pos.x > 0 ? 90 - rot.x : rot.x - 90);
            Laya.Vector3.lerp(this.role.transform.localRotationEuler.clone(), rot, 0.5, rot);
            this.role.transform.localRotationEuler = rot;
        }
        else {
            // this.lookAtTarget.transform.localPosition = this.role.transform.localPosition.clone();
            // this.lookAtTmpTarget.transform.position = this.role.transform.position.clone();
            // this.lookAtTmpTarget.transform.lookAt(this.lookAtTarget.transform.position.clone(), new Laya.Vector3(0, 0, 1));
            // let rot = this.lookAtTmpTarget.transform.localRotationEuler.clone();
            // // rot.setValue(oldRot.x, oldRot.y, pos.x > 0 ? 90 - rot.x : rot.x - 90);
            // rot.setValue(0, 0, pos.x > 0 ? 90 - rot.x : rot.x - 90);
            // this.role.transform.localRotationEuler = rot;

            this.role.transform.localRotationEuler = new Laya.Vector3;
        }
    }

    setRoadConfig(index) {
        if (this.curRoadIndex == index) return;
        this.curRoadIndex = index;
        this.maxPosX = this.roadConfig[index].roadWidth;
    }

    /**赛道配置 roadWidth:赛道宽度、 roadType:0_直线、1_椭圆、2_圆 */
    roadConfig: any[] = [
        {
            roadWidth: 3.5,
            roadType: 0
        },
        {
            roadWidth: 5,
            roadType: 1
        },
        {
            roadWidth: 5.5,
            roadType: 2
        },
        {
            roadWidth: 5,
            roadType: 0
        }
    ]
}

class RoutePoint {
    public position: Laya.Vector3;
    public direction: Laya.Vector3;

    constructor(position, direction) {
        this.position = position;
        this.direction = direction;
    }
}
