    onAwake() {
        CameraCrl.Instance = this;
        this.Display = this.owner as Laya.Camera;
        this.CameraState = CameraState.GameStart;
        this.FX_Fire1 = Utility.FindChildByName("FX_Fire1", this.Display) as Laya.Sprite3D;
        this.SpeedLine_1 = Utility.FindChildByName("SpeedLine_1", this.Display) as Laya.Sprite3D;
        this.FX_Fire1.active = false;
        this.SpeedLine_1.active = false;
        this.SpeedLine_1.transform.localRotationEulerX = 15;
        Laya.stage.on(Laya.Event.KEY_DOWN, this, this.onKeyDown);
    }

    onKeyDown(e: Laya.Event): void {
        if (e.keyCode == Laya.Keyboard.S) {
            this.isStop = true;
        }
    }

