    isEndMove: boolean = false;
    isStop: boolean = false;
    onLateUpdate() {
        // if (GameLogic.Share.isGameOver || !GameLogic.Share.isStartGame) {
        //     return
        // }
        if (this.isStop) return;
        switch (this.CameraState) {
            case CameraState.Main:
                // this.ChangeCameraMove(this.CameraMain_Pos, this.CameraMain_Rot, 0.05, 0.1);
                // this.MoveEvent();
                // this.RotaEvent();
                break;
            case CameraState.GameStart:
                this.ChangeCameraMove(this.CameraGameStart_Pos, this.CameraGameStart_Rot, 0.1, 0.05);
                // this.MoveEvent();
                // this.RotaEvent();
                break;
            case CameraState.Game:
                this.ChangeCameraMove(this.CameraGame_Pos, this.CameraGame_Rot, 0.1, 0.05);
                break;
            case CameraState.GameUp:
                this.ChangeCameraMove(this.CameraGame_Pos_Up_1, this.CameraGame_Rot_Up_1, 0.05, 0.05);
                break;
            case CameraState.GameDown:
                this.ChangeCameraMove(this.CameraGame_Pos_Down, this.CameraGame_Rot_Down, 0.05, 0.05);
                break;
            case CameraState.GameTurntable:
                this.ChangeCameraMove(this.CameraGame_Pos_Turntable, this.CameraGame_Rot_Turntable, 0.05, 0.05);
                // this.ChangeCameraMove(this.CameraGameEnd_Pos, this.CameraGameEnd_Rot, 0.05, 0.05);
                break;
            case CameraState.GameTurntableMore:
                this.ChangeCameraMove(this.CameraGame_Pos_Turntable_More, this.CameraGame_Rot_Turntable_More, 0.05, 0.05);
                // this.ChangeCameraMove(this.CameraGameEnd_Pos, this.CameraGameEnd_Rot, 0.05, 0.05);
                break;
            case CameraState.GameTurntable_3:
                this.ChangeCameraMove(this.CameraGame_Pos_Turntable_3, this.CameraGame_Rot_Turntable_3, 0.05, 0.05);
                break;
        }

        if (this.isEndMove) {
            // console.log("????")
        }
    }


    CameraTarget: Laya.Sprite3D = null;
    ChangeCameraMove(backPos: Laya.Vector3, backRot: Laya.Vector3, posSpeed: number, rotSpeed: number) {

        let mypos = this.Display.transform.localPosition.clone();
        // let aimPos = new Laya.Vector3(0, 2, 5);
        // let aimPos = new Laya.Vector3(
        //     this.CameraTarget.transform.position.x + backPos.x,
        //     // PlayerController.Instance.Display.transform.position.y + backPos.y + PlayerController.Instance.HaveBlockNum * 0.1,
        //     // PlayerController.Instance.Display.transform.position.z + backPos.z - PlayerController.Instance.HaveBlockNum * 0.1
        //     this.CameraTarget.transform.position.y + backPos.y,
        //     this.CameraTarget.transform.position.z + backPos.z
        // );
        let aimPos = backPos;
        let outpos = new Laya.Vector3();

        let myrot = this.Display.transform.localRotationEuler.clone();
        // let aimrot = new Laya.Vector3(-10, 0, 0);
        let aimrot = backRot;
        let outrot = new Laya.Vector3();

        Laya.Vector3.lerp(mypos, aimPos, posSpeed, outpos);
        this.Display.transform.localPosition = outpos;

        Laya.Vector3.lerp(myrot, aimrot, rotSpeed, outrot);
        this.Display.transform.localRotationEuler = outrot;
    }

