import PlayerDataMgr from "../Libs/PlayerDataMgr"
import Utility from "../Mod/Utility"
import SoundMgr from "../Mod/SoundMgr"
import SGAD from "../SGSDK/SGAD"
import GameLogic from "../Crl/GameLogic"
import EventMgr, { EventType } from "../Mod/EventManager"

export default class RewardUI extends Laya.Scene {
    constructor() {
        super()
    }

    NextBtn: Laya.Image;
    AllBox: Laya.Image;
    icon: Laya.Image;
    CoinIcon: Laya.Image;
    DiamondIcon: Laya.Image;
    BoxList: Laya.List;

    chooseId: number = 0;
    VideoIndexArr: number[] = [];
    boxArr: number[] = [0, 0, 0, 0, 0, 0,];
    openBoxNum: number = 0;
    randomClickNum: number = 3;

    onOpened() {
        SGAD.showBannerAd();
        this.RotationIndex = 0;
        this.size(Laya.stage.displayWidth, Laya.stage.displayHeight);
        this.openBoxNum = 0;
        this.randomClickNum = Utility.GetRandom(1, 3, true);
        this.boxArr = [0, 0, 0, 0, 0, 0, 0, 0, 0,];
        this.isShowVideo = false;
        this.NextBtn.visible = true;
        Utility.addClickEvent(this.NextBtn, this, this.closeBtnCB);

        this.initBoxList();
        // this.InitVidoeIndexArr();

        Laya.timer.frameLoop(1, this, this.myUpdate);
    }

    onClosed() {
        SGAD.hideBannerAd();
        Laya.timer.clearAll(this);
    }


    initBoxList() {
        this.AllBox.scaleX = this.AllBox.scaleY = 0;
        // this.BoxList.stopDrag();
        // this.BoxList.se
        // this.BoxList.vScrollBarSkin = '';
        this.BoxList.array = this.boxArr;
        this.BoxList.repeatX = 3;
        this.BoxList.repeatY = 3;
        this.BoxList.renderHandler = Laya.Handler.create(this, this.onBoxListRender, null, false);

        SoundMgr.instance.playSoundEffect("UIShow");
        Laya.Tween.to(this.AllBox, { scaleX: 1, scaleY: 1 }, 1000, Laya.Ease.bounceOut, Laya.Handler.create(this, () => {
        }));
    }

    InitVidoeIndexArr() {
        this.VideoIndexArr = [];
        for (let i = 0; i < 8; i++) {
            if (this.VideoIndexArr.length >= 3) {
                break;
            }
            let isCanAdd = true;
            let random = Utility.GetRandom(0, 8);
            let add = 0;
            if (this.VideoIndexArr.length <= 0) this.VideoIndexArr.push(random);
            for (let j = 0; j < this.VideoIndexArr.length; j++) {
                add++;
                if (random == this.VideoIndexArr[j]) {
                    isCanAdd = false;
                }
                if (add == this.VideoIndexArr.length && isCanAdd) {
                    this.VideoIndexArr.push(random);
                }
            }
        }
        // console.log(this.VideoIndexArr);
    }

    isShowVideo: boolean = false;
    onBoxListRender(cell, index: number) {
        if (index >= this.BoxList.array.length) {
            return;
        }
        var item = cell.getChildByName('item') as Laya.Image;
        let opbg = item.getChildByName('opbg') as Laya.Image;
        let Rotbox = item.getChildByName('Rotbox') as Laya.Image;
        let box = Rotbox.getChildByName('box') as Laya.Image;
        let video = Rotbox.getChildByName('video') as Laya.Image;
        let icon = Rotbox.getChildByName('icon') as Laya.Image;
        let num = Rotbox.getChildByName('num') as Laya.Label;
        let isRotationLoop = Rotbox.getChildByName('isRotationLoop') as Laya.Image;
        let getnum = 0;

        opbg.visible = this.boxArr[index] == 1;
        box.visible = this.boxArr[index] == 0;
        video.visible = false;
        if (this.isShowVideo) {
            if (this.boxArr[index] != 1)
                video.visible = true;
        } else {
            for (let i = 0; i < this.VideoIndexArr.length; i++) {
                if (index == this.VideoIndexArr[i] && this.boxArr[index] != 1) {
                    video.visible = true;
                }
            }
        }
        icon.visible = this.boxArr[index] == 1;
        // Laya.Tween.clearAll(item);
        if (this.boxArr[index] != 1 && !isRotationLoop.visible) {
            getnum = Utility.GetRandom(1, 5, true);
            num.text = getnum.toString();
            this.RotationLoop(Rotbox);
        }
        if (this.boxArr[index] == 1) {
            // console.log("清理抖动", Rotbox);
            Rotbox.clearTimer(this, this.RotationLoop);
            Laya.timer.clearAll(Rotbox);
            Laya.Tween.clearAll(Rotbox);
            Rotbox.rotation = 0;
        }
        num.visible = this.boxArr[index] == 1;

        isRotationLoop.visible = true;

        // icon.off(Laya.Event.CLICK, this, this.chooseCB);
        // icon.on(Laya.Event.CLICK, this, this.chooseCB, [index]);
        Utility.addClickEvent(item, this, this.buyBtnCB, [index, parseInt(num.text), video.visible, item]);
    }

    RotationIndex: number = 0;
    RotationLoop(item: any) {
        let image = item as Laya.Image;
        // if (index == 1) return;
        Laya.Tween.to(item, { rotation: -10 }, 100, Laya.Ease.linearIn, Laya.Handler.create(this, () => {
            Laya.Tween.to(item, { rotation: 10 }, 100, Laya.Ease.linearIn, Laya.Handler.create(this, () => {
                Laya.Tween.to(item, { rotation: 0 }, 100, Laya.Ease.linearIn, Laya.Handler.create(this, () => {
                    let func = () => {
                        this.RotationLoop(item);
                    }
                    image.timerOnce(1000, this, func);
                }));
            }));
        }));
    }

    showNextBtn() {
        this.NextBtn.scaleX = this.NextBtn.scaleY = 0;
        this.NextBtn.visible = true;
        Laya.Tween.to(this.NextBtn, { scaleX: 1, scaleY: 1 }, 500, Laya.Ease.bounceOut, Laya.Handler.create(this, () => {

        }));
    }

    buyBtnCB(arr: any[]) {
        let id: number = arr[0];
        let money: number = arr[1];
        let isshowVideo: boolean = arr[2];
        let item: Laya.Image = arr[3] as Laya.Image;
        // console.log("是否看广告", isshowVideo, money);
        if (this.boxArr[id] == 1) return;
        if (this.isShowVideo || isshowVideo) {
            let cb = () => {
                this.boxArr[id] = 1;
                this.openBoxNum++;
                PlayerDataMgr.getPlayerData().diamond += money;
                PlayerDataMgr.setPlayerData();
                if (this.openBoxNum >= this.randomClickNum) {
                    this.isShowVideo = true;
                    this.NextBtn.visible = true;
                }
                this.BoxList.array = this.boxArr;
                GameLogic.Share.SpawnUIAddDiamond(item, this.DiamondIcon, this, this, true);
            }
            SGAD.showVideoAd(cb);
        } else {
            this.boxArr[id] = 1;
            this.openBoxNum++;
            PlayerDataMgr.getPlayerData().diamond += money;
            PlayerDataMgr.setPlayerData();
            if (this.openBoxNum >= this.randomClickNum) {
                this.isShowVideo = true;
                this.NextBtn.visible = true;
            }
            this.BoxList.array = this.boxArr;
            GameLogic.Share.SpawnUIAddDiamond(item, this.DiamondIcon, this, this, true);
        }
        // console.log("拥有的宝石数量：", PlayerDataMgr.getPlayerData().diamond)
        // Laya.loader.create("prefab/DiamondFlyBox.json", Laya.Handler.create(this, ));
    }


    closeBtnCB() {
        this.NextBtn.offAll();
        SoundMgr.instance.playSoundEffect("UIShowDown");
        Laya.Tween.to(this.NextBtn, { scaleX: 0, scaleY: 0 }, 300, Laya.Ease.backIn, Laya.Handler.create(this, () => {

            SoundMgr.instance.playSoundEffect("UIShowDown");
            Laya.Tween.to(this.AllBox, { scaleX: 0, scaleY: 0 }, 300, Laya.Ease.backIn, Laya.Handler.create(this, () => {
                GameLogic.Share.closeReward();
            }));
        }));
    }

    myUpdate() {
    }
}