export class DateManager {
    private static _instance: DateManager;
    private Date: Date;
    static Day: number = 24 * 60 * 60 * 1000;
    static Hour: number = 60 * 60 * 1000;
    static Min: number = 60 * 1000;
    static Secound: number = 1000;
    static get instance(): DateManager {
        if (!this._instance) {
            this._instance = new DateManager();
        }
        return this._instance;
    }
    constructor() {
        this.Date = new Date();
    }
    get TimeStamp(): number {
        return this.Date.getTime();
    }
    /**获取相差天数 */
    getDifferDay(timeStamp: number) {
        let time = Math.abs(timeStamp - this.TimeStamp);
        return Math.floor(time / DateManager.Day);
    }
    getYear(timeStamp: number = this.TimeStamp) {
        let date = new Date(timeStamp);
        return date.getFullYear();
    }
    getMonth(timeStamp: number = this.TimeStamp) {
        let date = new Date(timeStamp);
        return date.getMonth();
    }
    getDate(timeStamp: number = this.TimeStamp) {
        let date = new Date(timeStamp);
        return date.getDate();
    }
    getHour(timeStamp: number = this.TimeStamp) {
        let date = new Date(timeStamp);
        return date.getHours();
    }
    getMin(timeStamp: number = this.TimeStamp) {
        let date = new Date(timeStamp);
        return date.getMinutes();
    }
    getSecound(timeStamp: number = this.TimeStamp) {
        let date = new Date(timeStamp);
        return date.getSeconds();
    }
    formatDateAdd(currStamp: number, day: number, isStamp: boolean = false): any {
        let time = currStamp + day * DateManager.Day;
        return isStamp ? time : this.formatDate(time);
    }
    formatDataRed(currStamp: number, day: number, isStamp: boolean = false): any {
        let time = currStamp - day * DateManager.Day;
        return isStamp ? time : this.formatDate(time);
    }
    formatDate(timeStamp: number = this.TimeStamp): string {
        let year = this.getYear(timeStamp);
        let month = this.getMonth(timeStamp) + 1;
        let date = this.getDate(timeStamp);
        return `${year}-${month}-${date}`;
    }
}