
import AIController from "../Crl/role/AIController"
import CameraCrl from "../Crl/CameraCrl"
import { CameraState } from "../Crl/Enum"
import GameLogic from "../Crl/GameLogic"
import { GameManager } from "../Crl/GameManager"
import PlayerController from "../Crl/role/PlayerController"
import PlayerDataMgr from "../Libs/PlayerDataMgr"
import EventMgr, { EventManager, EventType } from "../Mod/EventManager"
import SoundMgr from "../Mod/SoundMgr"
import CirculaGraphicsrCtr from "../Mod/UI/CirculaGraphicsrCtr"
import Utility from "../Mod/Utility"
import SGAD from "../SGSDK/SGAD"
import SGConfig from "../SGSDK/SGConfig"
import SGMgr from "../SGSDK/SGMgr"

export default class GameUI extends Laya.Scene {
    constructor() {
        super()
    }

    //主玩法界面
    GameingBox: Laya.Box;
    GameTipBox: Laya.Box;
    GameTipCloseBtn: Laya.Image;

    GameTimeBox: Laya.Box;
    GameingTimeLabel: Laya.Label;

    TopBox: Laya.Box;
    DiamondPro: Laya.Label;
    DiamondSystemLabel: Laya.Label;
    GameGetCoinTipBox: Laya.Image;
    PlayerUIPos: Laya.Image;
    GameGetCoinIcon: Laya.Image;

    SetBtn: Laya.Image;
    RocketBtn: Laya.Image;
    RankingBox: Laya.Box;
    GetCoinLabel: Laya.Label;
    RocketPro: Laya.Label;

    //暂停界面
    StopGameBox: Laya.Box;
    GameStopTimeLabel: Laya.Label;
    BackHomeBtn: Laya.Image;
    AgainBtn: Laya.Image;
    NextGameBtn: Laya.Image;

    //道具界面
    GamePropBox: Laya.Box;
    GamePropTitle: Laya.Image;
    ProBox_1: Laya.Image;
    PropCoinBtn_1: Laya.Image;
    PropVideoBtn_1: Laya.Image;

    ProBox_2: Laya.Image;
    PropDiamondBtn_2: Laya.Image;
    PropVideoBtn_2: Laya.Image;

    ProBox_3: Laya.Image;
    PropVideoBtn_3: Laya.Image;

    StatGameBtn: Laya.Image;
    GameHPBox: Laya.Box;


    onAwake() {
        // if (PlayerDataMgr.getPlayerData().grade != 1) {
        //     (this['ani1'] as Laya.FrameAnimation).stop()
        //     this.guide.visible = false
        // }
    }

    onOpened() {
        SoundMgr.instance.playMusic('Menu');
        this.size(Laya.stage.displayWidth, Laya.stage.displayHeight)
        // Utility.addClickEvent(this.propBtn, this, this.propBtnCB)
        // Laya.timer.frameLoop(1, this, this.myUpdate)

        SGMgr.inGame();

        this.onInitUI();
        Laya.timer.frameLoop(1, this, this.myUpdate);
        EventMgr.AddListener(EventType.onUpdateStayColliderNum, this, this.onUpdateStayColliderNum);
        EventMgr.AddListener(EventType.onUpdateDiamondBox, this, this.onUpdateDiamondBox);
        EventMgr.AddListener(EventType.onRestGameUI, this, this.onRestGameUI);
        EventMgr.AddListener(EventType.onChagePlayerHpBox, this, this.onChagePlayerHpBox);
    }
    onClosed() {
        Laya.timer.clearAll(this)
        EventMgr.OffAllCaller(this);
    }

    onInitUI() {
        GameManager.Instance.onRestAll();
        this.GameTipBox.visible = false;
        this.GameTipBox.on(Laya.Event.CLICK, this, () => {
            this.GameTipBox.visible = false;
        })
        // Utility.addClickEvent(this.GameTipCloseBtn, this, () => {
        // });
        this.onInitGamePropBox();
        this.onInitGameingBox();
    }

    onRestGameUI() {
        PlayerController.Instance.PlayerGameTime = 180;
        for (let i = 0; i < GameLogic.Share.AllAI.length; i++) {
            let ai = GameLogic.Share.AllAI[i].getComponent(AIController) as AIController;
            ai.PlayerGameTime = 180;
        }
        GameManager.Instance.onRestAll();
        this.onInitGamePropBox();
        this.onInitGameingBox();
    }

    //#region 游戏页面
    onInitGameingBox() {
        GameManager.Instance.PlayerRangNum = 1;
        GameManager.Instance.PlayerHpNum = 3;
        GameManager.Instance.PlayerGetCoinNum = 0;
        this.GetCoinLabel.text = GameManager.Instance.PlayerGetCoinNum.toString();

        this.onChagePlayerHpBox();

        PlayerController.Instance.PlayerGameTime = 180;
        for (let i = 0; i < GameLogic.Share.AllAI.length; i++) {
            let ai = GameLogic.Share.AllAI[i].getComponent(AIController) as AIController;
            ai.PlayerGameTime = 180;
        }
        this.GameingTimeLabel.text = Utility.countDownTimeHandler(PlayerController.Instance.PlayerGameTime);

        this.DiamondSystemLabel.text = PlayerDataMgr.getPlayerData().DiamondSystemNum.toString() + "/250";
        let pro = (1 - PlayerDataMgr.getPlayerData().DiamondSystemNum / 250) * 360;
        Laya.Tween.clearAll(this.DiamondPro);
        Laya.Tween.to(this.DiamondPro, { x: -pro }, 200, Laya.Ease.linearIn);

        for (let i = 0; i < this.RankingBox.numChildren; i++) {
            (this.RankingBox.getChildAt(i) as Laya.Image).visible = false;
        }
        (this.RankingBox.getChildAt(0) as Laya.Image).visible = true;

        Utility.addClickEvent(this.SetBtn, this, this.onSetBtnCB);
        Utility.addClickEvent(this.RocketBtn, this, this.onRocketBtnCB);

        this.GameingBox.on(Laya.Event.MOUSE_DOWN, this, this.onAddSpeedBtn_DownCB);
        this.GameingBox.on(Laya.Event.MOUSE_MOVE, this, this.touchMove)
        this.GameingBox.on(Laya.Event.MOUSE_UP, this, this.onDelSpeedBtn_DownCB);
    }

    onChagePlayerHpBox() {
        for (let i = 0; i < this.GameHPBox.numChildren; i++) {
            let hp = this.GameHPBox.getChildAt(i).getChildAt(0) as Laya.Image;
            if (i + 1 <= GameManager.Instance.PlayerHpNum) {
                hp.visible = true;
            } else {
                hp.visible = false;
            }
        }
    }

    isUseDoubleCoinProp: boolean = false;
    StayColliderNum: number = 0;
    isInStayCollider: boolean = false;
    /** 更新连续碰撞到物品的次数 */
    onUpdateStayColliderNum() {
        this.onUpdateRocketPro();
        if (this.isInStayCollider) {
            this.isInStayCollider = false;
            Laya.timer.clear(this, this.onUpdateIsInStayCollider);
        }
        this.isInStayCollider = true;
        this.StayColliderNum++;
        let showBox: Laya.Image;
        for (let i = 0; i < this.GameGetCoinTipBox.numChildren; i++) {
            let box = this.GameGetCoinTipBox.getChildAt(i) as Laya.Image;
            if (!box.visible) {
                showBox = box;
                break;
            }
        }
        let addNum = 1;
        if (this.isUseDoubleCoinProp) addNum = 2;
        let num = 0;
        for (let i = 1; i <= this.StayColliderNum; i++) { // 循环头：初始条件；循环条件；迭代条件
            num = num + i; // 累加语句
        }
        num *= addNum;
        (showBox.getChildAt(0) as Laya.Label).text = "+" + num.toString();
        showBox.alpha = 1;
        showBox.y = 306;
        showBox.visible = true;
        Laya.Tween.to(showBox, { y: 200, alpha: 0 }, 500, Laya.Ease.linearIn, new Laya.Handler(this, () => {
            showBox.visible = false;
        }))
        GameLogic.Share.SpawnUIAddCoin(this.PlayerUIPos, this.GameGetCoinIcon, this.GameingBox, this, false);
        Laya.timer.once(300, this, this.onUpdateIsInStayCollider);
    }
    onUpdateIsInStayCollider() {
        this.isInStayCollider = false;
        this.onUpdateGetCoinLabel(this.StayColliderNum);
        this.StayColliderNum = 0;
    }
    onUpdateGetCoinLabel(max: number) {
        let addNum = 1;
        if (this.isUseDoubleCoinProp) addNum = 2;
        let num = 0;
        for (let i = 1; i <= max; i++) { // 循环头：初始条件；循环条件；迭代条件
            num = num + i; // 累加语句
        }
        num *= addNum;
        GameManager.Instance.PlayerGetCoinNum += num;
        this.GetCoinLabel.text = GameManager.Instance.PlayerGetCoinNum.toString();
    }

    DiamondIcon: Laya.Image;
    onGetDiamondTipNum() {
        let showBox: Laya.Image;
        for (let i = 0; i < this.GameGetCoinTipBox.numChildren; i++) {
            let box = this.GameGetCoinTipBox.getChildAt(i) as Laya.Image;
            if (!box.visible) {
                showBox = box;
                break;
            }
        }
        (showBox.getChildAt(0) as Laya.Label).text = "+1";
        GameLogic.Share.SpawnUIAddDiamond(this.PlayerUIPos, this.DiamondIcon, this.GameingBox, this, false);
    }

    onUpdateGameTimeLabel() {
        PlayerController.Instance.PlayerGameTime--;
        for (let i = 0; i < GameLogic.Share.AllAI.length; i++) {
            let ai = GameLogic.Share.AllAI[i].getComponent(AIController) as AIController;
            if (ai.isStop) continue;
            ai.PlayerGameTime--;
        }
        this.GameingTimeLabel.text = Utility.countDownTimeHandler(PlayerController.Instance.PlayerGameTime);
        if (PlayerController.Instance.PlayerGameTime <= 0) {
            PlayerController.Instance.PlayerGameTime = 0;
            this.GameingTimeLabel.text = Utility.countDownTimeHandler(PlayerController.Instance.PlayerGameTime);
            Laya.timer.clear(this, this.onUpdateGameTimeLabel);
        }
    }

    /** 更新钻石任务 */
    onUpdateDiamondBox() {
        this.onGetDiamondTipNum();
        PlayerDataMgr.getPlayerData().DiamondSystemNum++;
        PlayerDataMgr.setPlayerData();
        if (PlayerDataMgr.getPlayerData().DiamondSystemNum >= 250) {
            PlayerDataMgr.getPlayerData().DiamondSystemNum = 0;
            PlayerDataMgr.getPlayerData().diamond += 100;
            PlayerDataMgr.setPlayerData();
        }
        this.DiamondSystemLabel.text = PlayerDataMgr.getPlayerData().DiamondSystemNum.toString() + "/250";

        let pro = (1 - PlayerDataMgr.getPlayerData().DiamondSystemNum / 250) * 360;
        Laya.Tween.clearAll(this.DiamondPro);
        Laya.Tween.to(this.DiamondPro, { x: -pro }, 200, Laya.Ease.linearIn);
    }

    onUpdateAIAndPlayerPos() {
        let pro = (1 - GameLogic.Share._ctr.progressDistance / GameLogic.Share._ctr.circuit.accumulateDistance) * 300;
        let pro_1 = (1 - GameLogic.Share._ctr_AI_1.progressDistance / GameLogic.Share._ctr_AI_1.circuit.accumulateDistance) * 300;
        let pro_2 = (1 - GameLogic.Share._ctr_AI_2.progressDistance / GameLogic.Share._ctr_AI_2.circuit.accumulateDistance) * 300;
        // let pro_3 = (1 - GameLogic.Share._ctr_AI_3.progressDistance / GameLogic.Share._ctr_AI_3.circuit.accumulateDistance) * 300;

        let arr = [
            { name: "Player", _pro: pro },
            { name: "AI_1", _pro: pro_1 },
            { name: "AI_2", _pro: pro_2 },
            //   { name: "AI_3", _pro: pro_3 }
        ];
        arr.sort(function (a: any, b: any) {
            return a._pro - b._pro;
        })
        // console.log("排名:   >>>>>   ", arr[0], arr[1])
        let index = 1;
        for (let i = 0; i < arr.length; i++) {
            if (arr[i].name == "Player") {
                break;
            }
            index++;
        }
        if (GameManager.Instance.PlayerRangNum == index) return;
        GameManager.Instance.PlayerRangNum = index;
        for (let i = 0; i < this.RankingBox.numChildren; i++) {
            (this.RankingBox.getChildAt(i) as Laya.Image).visible = false;
        }
        (this.RankingBox.getChildAt(index - 1) as Laya.Image).visible = true;
        // this.Rangking.value = index.toString();
    }

    onSetBtnCB() {
        this.onInitStopGameBox();
    }

    _rocketProNum: number = 0;
    _rocketProNumMax: number = 10;
    onUpdateRocketPro() {
        this._rocketProNum++;
        if (this._rocketProNum >= this._rocketProNumMax) {
            this._rocketProNum = this._rocketProNumMax;
            // return;
        }
        let pro = (1 - this._rocketProNum / this._rocketProNumMax) * 140;
        // this.RocketPro.y = pro;
        Laya.Tween.to(this.RocketPro, { y: pro }, 100, Laya.Ease.linearIn, Laya.Handler.create(this, () => {
            if (this._rocketProNum >= this._rocketProNumMax) {
                this.isReadyRocket = true;
                (this.RocketBtn.getChildAt(0) as Laya.Image).visible = true;
                if (PlayerDataMgr.getPlayerData().isFirstCanClickRocket) {
                    (this.RocketBtn.getChildAt(2) as Laya.Image).visible = true;
                    (this.RocketBtn.getChildAt(3) as Laya.Image).visible = true;
                }
            }
        }));
    }

    isReadyRocket: boolean = false;
    isAddSpeed: boolean = false;
    onRocketBtnCB() {
        if (!this.isReadyRocket) {
            Utility.showToast("技能还在冷却中！");
            return;
        }
        if (this.isAddSpeed) return;
        if (PlayerController.Instance.isInWater) return;
        if (PlayerDataMgr.getPlayerData().isFirstCanClickRocket) {
            PlayerDataMgr.getPlayerData().isFirstCanClickRocket = false;
            PlayerDataMgr.setPlayerData();
            (this.RocketBtn.getChildAt(2) as Laya.Image).visible = false;
            (this.RocketBtn.getChildAt(3) as Laya.Image).visible = false;
        }
        this.isReadyRocket = false;
        this._rocketProNum = 0;
        (this.RocketBtn.getChildAt(0) as Laya.Image).visible = false;
        this.RocketPro.y = 140;
        PlayerController.Instance.isInRocketSpeed = true;
        this.isAddSpeed = true;
        PlayerController.Instance.onAddBaseSpeedCB();
        //TODO:玩家开启极限加速
        SoundMgr.instance.playSoundEffect('BoosterSpeed');
        Laya.timer.once(3000, this, () => {
            this.isAddSpeed = false;
            PlayerController.Instance.isInRocketSpeed = false;
            PlayerController.Instance.onDelBaseSpeedCB();
        })
    }

    isFirstClick: boolean = true;
    // _clickNum: number = 0;
    onAddSpeedBtn_DownCB(evt: Laya.Event) {
        //TODO:玩家按住加速
        if (this.isFirstClick) {
            this.isFirstClick = false;
            CameraCrl.Instance.CameraState = CameraState.Game;
            EventMgr.SendEvent(EventType.AIStartGame);
        }
        // this._clickNum++;
        // Laya.timer.once(200, this, () => {
        //     this._clickNum = 0;
        // })
        PlayerController.Instance.playerMouseDown(evt)
        PlayerController.Instance.onAddSpeedCB();
    }
    // onAddSpeedBtn_UpCB() {
    //     //TODO:玩家松开加速
    //     PlayerController.Instance.onDelSpeedCB();
    // }

    onDelSpeedBtn_DownCB() {
        PlayerController.Instance.playerMouseUp();
        //TODO:玩家按住减速
        // PlayerController.Instance.onDelSpeedCB();
    }
    // onDelSpeedBtn_UpCB() {
    //     //TODO:玩家按住减速
    //     PlayerController.Instance.onDelSpeedCB();
    // }

    touchMove(evt: Laya.Event) {
        PlayerController.Instance.playerMouseMove(evt);
    }
    //#endregion

    //#region 暂停界面
    stopGameSpeed: number = 0;
    onInitStopGameBox() {
        EventMgr.SendEvent(EventType.onStopGame);
        Laya.timer.clear(this, this.onUpdateGameTimeLabel);
        this.stopGameSpeed = GameLogic.Share.speedFactor;
        GameLogic.Share._ctr.speedFactor = GameLogic.Share.speedFactor = 0;
        this.GameStopTimeLabel.text = Utility.countDownTimeHandler(PlayerController.Instance.PlayerGameTime);

        this.StopGameBox.scale(0, 0);
        this.StopGameBox.visible = true;
        SoundMgr.instance.playSoundEffect("UIShow");
        Laya.Tween.to(this.StopGameBox, { scaleX: 1, scaleY: 1 }, 300, Laya.Ease.backOut);

        Utility.addClickEvent(this.BackHomeBtn, this, this.onBackHomeBtnCB);
        Utility.addClickEvent(this.AgainBtn, this, this.onAgainBtnCB);
        Utility.addClickEvent(this.NextGameBtn, this, this.onNextGameBtnCB);
    }

    onBackHomeBtnCB() {
        SoundMgr.instance.playSoundEffect("UIShowDown");
        Laya.Tween.to(this.StopGameBox, { scaleX: 1, scaleY: 1 }, 300, Laya.Ease.backIn, Laya.Handler.create(this, () => {
            GameLogic.Share.restartGame();
            Laya.Scene.open('MyScenes/StartUI.scene');
        }));
    }

    onAgainBtnCB() {
        // GameLogic.Share._ctr.reset(0, 2);
        // this.onNextGameBtnCB();
        SoundMgr.instance.playSoundEffect("UIShowDown");
        Laya.Tween.to(this.StopGameBox, { scaleX: 1, scaleY: 1 }, 300, Laya.Ease.backIn, Laya.Handler.create(this, () => {
            GameLogic.Share.restartGame(true);
            Laya.Scene.open('MyScenes/GameUI.scene');
        }));
    }

    onNextGameBtnCB() {
        SoundMgr.instance.playSoundEffect("UIShowDown");
        Laya.Tween.to(this.StopGameBox, { scaleX: 1, scaleY: 1 }, 300, Laya.Ease.backIn, Laya.Handler.create(this, () => {
            Laya.timer.loop(1000, this, this.onUpdateGameTimeLabel);
            GameLogic.Share._ctr.speedFactor = GameLogic.Share.speedFactor = this.stopGameSpeed;
            this.StopGameBox.visible = false;
            EventMgr.SendEvent(EventType.AIStartGame);
        }));
    }
    //#endregion

    //#region 道具页面
    onInitGamePropBox() {
        this.GameingBox.visible = false;
        this.StopGameBox.visible = false;
        this.PropCoinBtn_1.visible = false;
        this.PropVideoBtn_1.visible = false;
        this.PropDiamondBtn_2.visible = false;
        this.PropVideoBtn_2.visible = false;

        this.GamePropTitle.scale(0, 0);
        this.ProBox_1.scale(0, 0);
        this.ProBox_2.scale(0, 0);
        this.ProBox_3.scale(0, 0);
        this.StatGameBtn.scale(0, 0);
        SoundMgr.instance.playSoundEffect("UIShow");
        Laya.Tween.to(this.GamePropTitle, { scaleX: 1, scaleY: 1 }, 300, Laya.Ease.backOut, Laya.Handler.create(this, () => {

            SoundMgr.instance.playSoundEffect("UIShow");
            Laya.Tween.to(this.ProBox_1, { scaleX: 1, scaleY: 1 }, 300, Laya.Ease.backOut);
            Laya.Tween.to(this.ProBox_2, { scaleX: 1, scaleY: 1 }, 300, Laya.Ease.backOut);
            Laya.Tween.to(this.ProBox_3, { scaleX: 1, scaleY: 1 }, 300, Laya.Ease.backOut, Laya.Handler.create(this, () => {

                SoundMgr.instance.playSoundEffect("UIShow");
                Laya.Tween.to(this.StatGameBtn, { scaleX: 1, scaleY: 1 }, 300, Laya.Ease.backOut);
            }));

        }));

        this.PropCoinBtn_1.visible = PlayerDataMgr.getPlayerData().coin >= GameManager.Instance.ShieldPropNeedCoin;
        this.PropVideoBtn_1.visible = PlayerDataMgr.getPlayerData().coin < GameManager.Instance.ShieldPropNeedCoin;

        this.PropDiamondBtn_2.visible = PlayerDataMgr.getPlayerData().diamond >= GameManager.Instance.DoubleCoinPropNeedDiamond;
        this.PropVideoBtn_2.visible = PlayerDataMgr.getPlayerData().diamond < GameManager.Instance.DoubleCoinPropNeedDiamond;

        Utility.addClickEvent(this.PropCoinBtn_1, this, this.onPropCoinBtnCB);
        Utility.addClickEvent(this.PropVideoBtn_1, this, this.onPropVideoBtnCB_1);
        Utility.addClickEvent(this.PropDiamondBtn_2, this, this.onPropDiamondBtnCB);
        Utility.addClickEvent(this.PropVideoBtn_2, this, this.onPropVideoBtnCB_2);
        Utility.addClickEvent(this.PropVideoBtn_3, this, this.onPropVideoBtnCB_3);
        Utility.addClickEvent(this.StatGameBtn, this, this.onStartGameBtnCB);
    }

    onPropCoinBtnCB() {
        //TODO:玩家获得护盾回调
        PlayerDataMgr.getPlayerData().coin -= GameManager.Instance.ShieldPropNeedCoin;
        PlayerDataMgr.setPlayerData();
        PlayerController.Instance.isInvincible = true;
        PlayerController.Instance.onInInvincibleTime(true);
        this.onStartGameBtnCB();
    }

    onPropDiamondBtnCB() {
        //TODO:玩家获得双倍金币回调
        this.isUseDoubleCoinProp = true;
        PlayerDataMgr.getPlayerData().diamond -= GameManager.Instance.DoubleCoinPropNeedDiamond;
        PlayerDataMgr.setPlayerData();
        this.onStartGameBtnCB();
    }

    onPropVideoBtnCB_1() {
        Utility.showVideoAd(() => {
            PlayerController.Instance.isInvincible = true;
            PlayerController.Instance.onInInvincibleTime(true);
            this.onStartGameBtnCB();
        }, () => { Utility.showToast("看完视频才能获得奖励噢!") })
    }

    onPropVideoBtnCB_2() {
        Utility.showVideoAd(() => {
            this.isUseDoubleCoinProp = true;
            this.onStartGameBtnCB();
        }, () => { Utility.showToast("看完视频才能获得奖励噢!") })
    }

    onPropVideoBtnCB_3() {
        Utility.showVideoAd(() => {
            //TODO:免费试用美人鱼
            PlayerDataMgr.getPlayerData().freeskinId = 9;
            PlayerDataMgr.setPlayerData();
            EventMgr.SendEvent(EventType.ChangeSkin, []);
            this.onStartGameBtnCB();
        }, () => { Utility.showToast("看完视频才能获得奖励噢!") })
    }

    onStartGameBtnCB() {
        if (Laya.Browser.onWeiXin && !SGConfig.data.front_game_banner) {
            SGAD.hideBannerAd();
        }
        SoundMgr.instance.playSoundEffect("UIShowDown");
        Laya.Tween.to(this.GamePropTitle, { scaleX: 0, scaleY: 0 }, 300, Laya.Ease.backIn);
        Laya.Tween.to(this.ProBox_1, { scaleX: 0, scaleY: 0 }, 300, Laya.Ease.backIn);
        Laya.Tween.to(this.ProBox_2, { scaleX: 0, scaleY: 0 }, 300, Laya.Ease.backIn);
        Laya.Tween.to(this.ProBox_3, { scaleX: 0, scaleY: 0 }, 300, Laya.Ease.backIn);
        Laya.Tween.to(this.StatGameBtn, { scaleX: 0, scaleY: 0 }, 300, Laya.Ease.backIn, Laya.Handler.create(this, () => {
            this.GameingBox.visible = true;
            this.GamePropBox.visible = false;
        }));
        if (PlayerDataMgr.getPlayerData().isFirstGame) {
            PlayerDataMgr.getPlayerData().isFirstGame = false;
            PlayerDataMgr.setPlayerData();
            this.GameTipBox.visible = true;
        }
        GameLogic.Share._ctr.speedFactor = GameLogic.Share.speedFactor = 0;
        Laya.timer.loop(1000, this, this.onUpdateGameTimeLabel);
    }
    //#endregion


    myUpdate() {
        this.onUpdateAIAndPlayerPos();
        let out = new Laya.Vector4();
        CameraCrl.Instance.Display.viewport.project(PlayerController.Instance.Display.transform.position, CameraCrl.Instance.Display.projectionViewMatrix, out);
        this.PlayerUIPos.x = (out.x / Laya.stage.clientScaleX);
        this.PlayerUIPos.y = (out.y / Laya.stage.clientScaleY);
    }

}
