import PlayerDataMgr from "../../Libs/PlayerDataMgr";
import EventMgr, { EventType } from "../../Mod/EventManager";
import Utility from "../../Mod/Utility";
import GameLogic from "../GameLogic";
import PlayerController from "../role/PlayerController";

export default class CoinController extends Laya.Script3D {

    Display: Laya.Sprite3D;
    Gold: Laya.Sprite3D
    FX_GetGold_1: Laya.Sprite3D;

    onAwake() {
        this.Display = this.owner as Laya.Sprite3D;
        this.Gold = Utility.FindChildByName("Gold", this.Display) as Laya.Sprite3D;
        this.FX_GetGold_1 = Utility.FindChildByName("FX_GetGold_1", this.Display) as Laya.Sprite3D;
    }


    GetMoney() {
        this.Display.getComponent(Laya.PhysicsCollider).enable = false;
        this.Gold.active = false;
        this.FX_GetGold_1.active = true;
        EventMgr.SendEvent(EventType.onUpdateStayColliderNum);
    }

    onChangeCoinPos(num: number) {
        // console.log("修改位置>>>>>  ", num)
        // this.Display.transform.position.x += num;
    }

    isDestroy: boolean = false;
    onDestroy(): void {
        this.isDestroy = true;
    }

    onUpdate(): void {
        if (this.isDestroy) return;
        this.Gold.transform.localRotationEulerY -= 1;
        // this.isGetMoney();
    }
}